import * as React from "react";
import cookie from 'react-cookies';
import { Button } from "reactstrap";

interface Step3Props {
    onUpload: (files: FileList) => void
}

interface Step3Stat {
    files: FileList | null,
    isPremium: string
}

export class Step3 extends React.Component<Step3Props, Step3Stat> {

    constructor(props: Step3Props) {
        super(props);
        this.state = {
            files: null,
            isPremium: "true"
        };
    }

    onChangeFiles = (files: FileList) => {
        this.setState((prevState) => ({
            files: files
        }));
    }

    onUpload = () => {
        if (this.state.isPremium != "true") {
            return;
        }

        this.props.onUpload(this.state.files as FileList);
    }

    render() {
        return (<div className="row" style={{ width: "100%" }}>
            <div className="col-xs-12">
                <div className="form-group">
                    <label htmlFor="Files">Photos</label>
                    <input accept="image/*" type="file" multiple={true} className="form-control" id={"Files"} onChange={(e) => this.onChangeFiles(e.target.files as FileList)} />
                </div>
            </div>
            <div className="col-xs-12" style={{ textAlign: "center" }}>
                <img src="/assets/img/uploadcloud.png" style={{ margin: "auto" }} />
            </div>
            <div className="col-xs-12" style={{ textAlign: "center" }}>
                {this.state.files != null && this.state.files.length > 0 && <Button style={{ width: "100%" }} onClick={this.onUpload} bsStyle="primary" disabled={this.state.isPremium != "true"}>
                    {this.state.isPremium != "true" ? <span>Uploading only for Premium users</span> : <span>Start uploading</span>}
                </Button>}
            </div>
        </div>);
    }
}

export default Step3;