import * as Cookie from "js-cookie";
import Axios from "axios";
import { EventClient } from "../../../../../api/eventsClient";
import store2 from "store2";

export interface IShoppingCart {
    getOrder(peronalData: IPersonalData, photoList: any[]): IOrder;
    submitOrder(order: IOrder, eventId: number);
    submitOrder2(order: IOrder, eventId: number);
    getItem(photoId: number): ShoppingCartItem;
    clear();
    hasFormat(photoId: number, formatIndex: number): boolean;
    getFormatItemCount(formatIndex: number): number;
    allPhotoCount(): number
}

export class OrderShoppingCart implements IShoppingCart {

    shoppingCarts: ShoppingCart[] = [];

    constructor(foldersIds: number[]) {
        foldersIds.forEach(folderId => {
            this.shoppingCarts.push(new ShoppingCart(folderId));
        });
    }

    getOrder(peronalData: IPersonalData, photoList: { Id: number, FileName: string, FileSize: number }[]): IOrder {
        let order: IOrder = {
            Id: 0,
            ClientFirstName: peronalData.ClientFirstName,
            PhoneNumber: peronalData.PhoneNumber,
            ClientLastName: peronalData.ClientLastName,
            Email: peronalData.Email,
            ExtraInformation: peronalData.ExtraInformation,
            PhotoList: []
        }

        this.shoppingCarts.forEach(sc => {
            order.Id = sc.folderId;
            var folderOrder = sc.getOrder(peronalData, photoList);
            order.PhotoList = order.PhotoList.concat(folderOrder.PhotoList);

        });
        return order;
    }

    submitOrder(order: IOrder, eventId: number) {
        Axios.post("/Event/GetXmlForOrder", order).then((response) => {
            window.location.replace("/Event/EventFoldersList?completed=true&eventId=" + eventId);
        });
    }

    submitOrder2(order: IOrder, eventId: number) {
        var eventClient = new EventClient();
        return eventClient.GetXmlForOrder(order)
    }

    getItem(photoId: number): ShoppingCartItem {
        let item: any = null;
        this.shoppingCarts.forEach(sc => {
            let scItem = sc.getItem(photoId);
            if (scItem) {
                item = scItem;
            }
        });
        return item;
    }
    clear() {
        this.shoppingCarts.forEach(shoppingCart => {
            shoppingCart.clear();
        });
    }
    hasFormat(photoId: number, formatIndex: number): boolean {
        let hasFormat: boolean = false;
        this.shoppingCarts.forEach(sc => {
            if (sc.hasFormat(photoId, formatIndex)) {
                hasFormat = true;
            }
        });
        return hasFormat;
    }
    getFormatItemCount(formatIndex: number): number {
        let count: number = 0;
        this.shoppingCarts.forEach(sc => {
            count += sc.getFormatItemCount(formatIndex);
        });
        return count;
    }
    allPhotoCount(): number {
        let count: number = 0;
        this.shoppingCarts.forEach(sc => {
            count += sc.allPhotoCount();
        });
        return count;
    }


}

export class ShoppingCart implements IShoppingCart {
    public folderId: number = 0;
    photos: ShoppingCartItem[] = [];

    constructor(folderId: number) {
        let cookieData = Cookie.get(`EventPhotoCart${folderId}`);
        this.folderId = folderId;
        if (cookieData) {
            var data = cookieData.split(",");

            for (var i = 0; i < data.length; i++) {
                this.photos.push(new ShoppingCartItem(data[i]));
            }
        }
    }

    clear() {
        let store = store2.namespace("EventPhotoCart");
        store.remove(`EventPhotoCart${this.folderId}`);
        Cookie.remove(`EventPhotoCart${this.folderId}`)
    }

    getOrder(peronalData: IPersonalData, photoList: { Id: number, FileName: string, FileSize: number }[]): IOrder {
        let order: IOrder = {
            Id: this.folderId,
            ClientFirstName: peronalData.ClientFirstName,
            PhoneNumber: peronalData.PhoneNumber,
            ClientLastName: peronalData.ClientLastName,
            Email: peronalData.Email,
            ExtraInformation: peronalData.ExtraInformation,
            PhotoList: []
        }

        this.photos.forEach((photo) => {

            let photoItem = photoList.find(p => p.Id == photo.photoId);

            let p: IPhotoListItem = {
                Count: 0,
                FileName: photoItem ? photoItem.FileName : "",
                FileSize: photoItem ? photoItem.FileSize : 0,
                FolderId: this.folderId,
                Id: photo.photoId,
                IsAlbum: false,
                Order: []
            }

            photo.formatCount.forEach((value, index) => {
                p.Order.push({ OrderType: this.orderTypeMapping(index), Quantity: value });
            });

            order.PhotoList.push(p);
        });

        return order;
    }

    orderTypeMapping(orderTypeIndex: number) {
        switch (orderTypeIndex) {
            case 0: return 1;
            case 1: return 2;
            case 2: return 3;
            case 3: return 0;
            default: return 1;
        }
    }

    submitOrder(order: IOrder, eventId: number): void {
        Axios.post("/Event/GetXmlForOrder", order).then((response) => {
            window.location.replace("/Event/EventFoldersList?completed=true&eventId=" + eventId);
        });
    }

    submitOrder2(order: IOrder, eventId: number) {
        var eventClient = new EventClient();
        return eventClient.GetXmlForOrder(order)
    }

    getItem(photoId: number): ShoppingCartItem {
        return this.photos.find((item) => { return item.photoId == photoId }) as any;
    }

    hasFormat(photoId: number, formatIndex: number): boolean {
        let photo = this.getItem(photoId);
        if (photo && photo.getFormatCount(formatIndex) > 0) {
            return true;
        }

        return false
    }

    addItem(photoId: number, formatIndex: number, value: number) {
        let existed = this.photos.find((item) => { return item.photoId == photoId });
        if (existed) {
            existed.addFormatCount(formatIndex, value);
        } else {
            let item = new ShoppingCartItem();
            item.photoId = photoId;
            item.addFormatCount(formatIndex, value);
            this.photos.push(item);
        }
    }

    getFormatItemCount(formatIndex: number): number {
        let count = 0;
        this.photos.forEach(p => { count += p.getFormatCount(formatIndex) });
        return count;
    }

    allPhotoCount(): number {
        let count = 0;
        this.photos.forEach(photo => {
            photo.formatCount.forEach(fc => {
                count += fc;
            });
        });
        return count;
    }

    toString() {
        let data: string[] = [];
        this.photos.forEach(p => {
            data.push(p.toString());
        });
        return data.join(",");
    }

    save() {
        let cookie = Cookie.withConverter({
            write: (value, name) => {
                return value;
            }
        })
        cookie.set(`EventPhotoCart${this.folderId}`, this.toString(), { expires: 365 });
        let store = store2.namespace("EventPhotoCart");
        store.set(`EventPhotoCart${this.folderId}`, this.toString());
    }

    public static addItem(folderId: number, photoId: number, formatIndex: number, value: number) {
        let sc = new ShoppingCart(folderId);
        sc.addItem(photoId, formatIndex, value);
        sc.save();
    }
}

export class ShoppingCartItem {
    public photoId: number = 0;

    public formatCount = [0, 0, 0, 0];

    constructor(data?: string) {
        if (data) {

            let info = data.split(":");
            this.photoId = Number(info[0]);
            this.formatCount = [Number(info[1]), Number(info[2]), Number(info[3]), Number(info[4])]
        }
    }

    addFormatCount(formatIndex: number, value: number) {
        if (value < 0) {
            return;
        }
        this.formatCount[formatIndex] = value;
    }

    getFormatCount(formatIndex: number) {
        if (formatIndex > this.formatCount.length - 1) {
            return 0;
        }
        return this.formatCount[formatIndex];
    }

    hasAnyFormat(): boolean {
        let count = 0;
        for (let i = 0; i < this.formatCount.length; i++) {
            count += this.getFormatCount(i);
        }

        return count > 0;
    }

    toString() {
        return `${this.photoId}:${this.formatCount[0]}:${this.formatCount[1]}:${this.formatCount[2]}:${this.formatCount[3]}`;
    }
}

export interface IPersonalData {
    PhoneNumber: string,
    ClientFirstName: string,
    ClientLastName: string,
    Email: string,
    ExtraInformation: string,
}

export interface IOrder {
    Id: number,
    PhoneNumber: string,
    ClientFirstName: string,
    ClientLastName: string,
    Email: string,
    ExtraInformation: string,
    PhotoList: IPhotoListItem[]
}

export interface IPhotoListItem {
    IsAlbum: boolean,
    Id: number,
    Count: number,
    FileName: string,
    FileSize: number, //do pobrania
    FolderId: number,
    Order: IOrderItem[]
}

export interface IOrderItem {
    Quantity: number,
    OrderType: number,

}