import React from "react";
import { IImage } from "../ImageLightbox";
import { IsMobile as isMobile } from '../Helpers/IsMobileHelper';
import "./ImageLightBoxHeader.scss";
import { Favourites } from "../Models/Favourites";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHeart, faTimes, } from '@fortawesome/free-solid-svg-icons'
import { faHeart as fasHeart } from "@fortawesome/free-regular-svg-icons"

export class ImageLightboxHeader extends React.Component<any, any> {

    state = {
        change: false
    }


    onClose = () => {
        this.props.modalProps.onClose();
    }

    toggleShopping = () => {
        if (this.props.toggleShopping) {
            this.props.toggleShopping();
        }
    }

    addToFavourite = () => {
        let data: IImage = this.props.currentView;
        if (Favourites.isFavourite(data.folderId, data.photoId)) {
            Favourites.removeItem(data.folderId, data.photoId);
        } else {
            Favourites.addItem(data.folderId, data.photoId);
        }

        this.setState({ change: !this.state.change })
    }

    isFavourite = (): boolean => {
        let data: IImage = this.props.currentView
        return Favourites.isFavourite(data.folderId, data.photoId);
    }

    render() {
        let data: IImage = this.props.currentView
        return (<div className="imageLightboxHeader">
            <div className="leftContainer">
                <div className="folderName">{data.folder}</div>
                <div className="fileName">{data.filename}</div>
            </div>
            <div className="rightContainer">
                {!this.props.isAdmin && !isMobile() && <div className="favouriteButton" onClick={this.addToFavourite} >{!this.isFavourite() ? <FontAwesomeIcon icon={fasHeart} /> : <FontAwesomeIcon icon={faHeart} />}</div>}
                {!this.props.isAdmin && !isMobile() && <div className="shoppingButton" onClick={this.toggleShopping}><img src="/Content/Images/productIcon-bold.png" /></div>}
                <div className="closeButton" onClick={this.onClose}><FontAwesomeIcon icon={faTimes} /></div>
            </div>
        </div>)
    }
}

