import * as React from "react";
import axios from "axios";
import { ImageSizer } from "../ImageSizer";
import { EventClient } from "../../api/eventsClient";


interface ParallaxItemProps {
    coverPhoto: string,
    top: number,
    canEdit: boolean,
    objectId: number,
    mode: string
}

interface ParallaxItemState {
    proportions: number,
    top: number,
    isDragging: boolean,
    originalY: number,
    translateY: number,
    lastTranslateY: number,
    edit: boolean,
    canEdit: boolean,
    showArrow: boolean,
    backgroundNaturalWidth: number,
    backgroundNaturalHeight: number,

    windowWidth: number;
    scrolled: number;
}

export class ParallaxItem extends React.Component<ParallaxItemProps, ParallaxItemState> {

    eventClient: EventClient;

    heightPercent: number = 0.75;

    public static defaultProps: Partial<ParallaxItemProps> = {
        top: 0,
        canEdit: true
    };

    constructor(props: ParallaxItemProps) {
        super(props);

        this.eventClient = new EventClient();

        this.state = {
            proportions: this.props.top,
            top: this.getOffsetFromProportions(this.props.top),
            isDragging: false,
            originalY: 0,
            translateY: 0,
            lastTranslateY: 0,

            edit: false,
            canEdit: false,
            showArrow: false,

            backgroundNaturalHeight: 0,
            backgroundNaturalWidth: 0,
            windowWidth: window.innerWidth,
            scrolled: 0
        }
    }

    componentDidMount() {
        window.addEventListener("resize", this.dimensionUpdateEvent);
        window.addEventListener("scroll", this.parallaxEffectRun);
    }


    componentWillUnmount() {
        window.removeEventListener('mousemove', this.handleMouseMove);
        window.removeEventListener('mouseup', this.handleMouseUp);
        window.removeEventListener("scroll", this.parallaxEffectRun);
    }

    parallaxEffectRun = () => {
        this.setState(prevState => ({
            ...prevState,
            scrolled: document.body.scrollTop || document.documentElement.scrollTop
        }));
    }


    dimensionUpdateEvent = () => {
        this.setState(prevState => ({
            ...prevState,
            top: this.getOffsetFromProportions(this.state.proportions),
            windowWidth: window.innerWidth
        }));
    }

    get backgroundHeight() {
        if (this.state.windowWidth > 992) {
            return this.vhToPixels(this.heightPercent * 100) + "px";
        } else {
            return (this.vwToPixels(100) * this.state.backgroundNaturalHeight) / this.state.backgroundNaturalWidth;
        }
    }

    handleMouseDown = ({ clientX, clientY }) => {
        if (!this.props.canEdit) {
            return;
        }

        window.addEventListener('mousemove', this.handleMouseMove);
        window.addEventListener('mouseup', this.handleMouseUp);

        this.setState({
            originalY: clientY,
            isDragging: true
        });
    };

    handleMouseMove = ({ clientX, clientY }) => {
        const { isDragging } = this.state;
        if (!isDragging) {
            return;
        }


        let translateY = clientY - this.state.originalY + this.state.lastTranslateY;


        let positionTop = this.getOffsetFromProportions(this.props.top) + translateY;
        if (positionTop > 0 || positionTop < this.maxTop) {
            return;
        }

        this.setState(prevState => ({
            proportions: this.getProportionsFromOffset(positionTop),
            top: positionTop,
            translateY: translateY
        }));
    };

    get maxTop(): number {

        let windowWidth = this.vwToPixels(100);
        let windowHeight = this.vhToPixels(this.heightPercent * 100);

        let realImageWidth = this.state.backgroundNaturalWidth;
        let realImageHeight = this.state.backgroundNaturalHeight;

        let scaledImageHeight = (windowWidth * realImageHeight) / realImageWidth;
        if (windowHeight < scaledImageHeight) {
            return windowHeight - scaledImageHeight + 20;
        } else {
            return scaledImageHeight - windowHeight + 20;
        }
    }

    vhToPixels(vh: number) {
        return Math.round(window.innerHeight / (100 / vh));
    }

    vwToPixels(vw: number) {
        return Math.round(window.innerWidth / (100 / vw));
    }

    getProportionsFromOffset = (offsetTop: number) => {
        return offsetTop / window.innerWidth;
    }

    getOffsetFromProportions = (proportions: number) => {
        return proportions * window.innerWidth;
    }


    handleMouseUp = () => {
        window.removeEventListener('mousemove', this.handleMouseMove);
        window.removeEventListener('mouseup', this.handleMouseUp);

        this.setState(
            {
                originalY: 0,
                lastTranslateY: this.state.translateY,

                isDragging: false
            },
            () => {
                this.updateEventHeaderPosition();
            }
        );
    };

    onWheel = (e: React.WheelEvent<HTMLDivElement>) => {
        e.stopPropagation();
    }

    onMouseEnter = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        if (this.props.canEdit) {
            this.setState({ showArrow: true });
        }

    }
    onMouseLeave = (e: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        this.setState({ showArrow: false });
    }

    updateEventHeaderPosition() {
        this.eventClient.SetCoverOffset(this.props.objectId, this.getProportionsFromOffset(this.state.top), this.props.mode);
    }

    get backgroundPositionY(): number {
        if (this.props.canEdit) {
            return this.state.top;
        } else {
            if (this.state.windowWidth > 992) {
                return (this.state.top + this.state.scrolled * 0.5);
            } else {
                return (this.state.scrolled * 0.5);
            }
        }

    }

    setBacgkroundSizes = (width: number, height: number) => {
        this.setState(state => {
            return {
                ...state,
                backgroundNaturalHeight: height,
                backgroundNaturalWidth: width
            }
        });
    }

    render() {
        return (<>
            {this.props.coverPhoto &&
                <div className="parallaxItem">
                    <ImageSizer src={this.props.coverPhoto.replace(/ /g, "%20")} onLoad={(w, h) => this.setBacgkroundSizes(w, h)} />
                    <div onDragStart={e => e.preventDefault()} onMouseEnter={(e) => this.onMouseEnter(e)} onMouseLeave={(e) => this.onMouseLeave(e)} onWheel={(event) => this.onWheel(event)} onMouseDown={this.handleMouseDown} className="parallax" id="marginBot" style={{ display: "flex", backgroundSize: "cover", justifyContent: "flex-end", alignItems: "center", backgroundImage: `url(${this.props.coverPhoto.replace(/ /g, "%20")})`, backgroundPositionY: this.backgroundPositionY, backgroundPositionX: "center", width: "100%", height: this.backgroundHeight, marginTop: "0", backgroundRepeat: "no-repeat", position: "relative", overflow: "hidden" }} >
                        {this.state.showArrow && <div className="disableSelection" style={{
                            outline: "none",
                            height: "200px",
                            right: "-60px",
                            position: "absolute",
                            WebkitTouchCallout: "none",
                            WebkitUserSelect: "none",
                            KhtmlUserSelect: "none",
                            MozUserSelect: "none",
                            msUserSelect: "none",
                            userSelect: "none"
                        }}>
                            <img className="disableSelection" style={{
                                height: "100%",
                            }} src="/assets/img/moveImage.png" /></div>}
                    </div>
                </div>}
        </>
        );
    }
}


export default ParallaxItem;