import Axios from "axios";
import * as settings from "./settings";
import { IApiResponse } from "./IApiResponse";






export class HomeClient {

    PhotographerDetails() {
        return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/home/photographerDetails`, settings.CONFIG);
    }

    Register(data: any) {
        return Axios.post<IApiResponse<any>>(`${settings.BASE_URL}/home/register`, data, settings.CONFIG);
    }
}