import React from "react";
import { IsMobile as isMobile, IsMobile } from '../Helpers/IsMobileHelper';
import "./ImageLightBoxView.scss";
import { LightBoxShopping } from "./LightBoxShopping";
import * as classnames from "classnames";
import { Swipeable } from "react-swipeable";
import { TransformWrapper, TransformComponent } from "react-zoom-pan-pinch";

export interface IImageLightBoxViewState {
    showShopping: boolean,
    zoom: boolean
}

export class ImageLightBoxView extends React.Component<any, IImageLightBoxViewState> {

    constructor(props) {
        super(props);


        this.state = {
            showShopping: this.props.showShopping,
            zoom: false
        }
    }

    toggleShopping = () => {
        this.setState({ showShopping: !this.state.showShopping });
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.showShopping != this.state.showShopping) {
            this.setState({ showShopping: nextProps.showShopping });
        }
    }

    get showShopping(): boolean {
        return this.state.showShopping;
    }

    onSwipe = (side: "up" | "down") => {
        if (side == "down") {
            this.setState({ showShopping: false });
        } else if (side == "up") {
            this.setState({ showShopping: true });
        }
    }

    onHideShopping = () => {
        if (this.props.changeShopping) {
            this.props.changeShopping(false);
        }
    }

    render() {
        return (
            <Swipeable onSwipedDown={() => this.onSwipe("down")} onSwipedUp={() => this.onSwipe("up")}>

                <div className="imageLightBoxView">

                    <div className={"imgContainer" + (this.showShopping ? " margin-80" : "")}>
                        <img alt={this.props.data.filename} src={this.props.data.src} />
                    </div>


                    {this.showShopping && <div className={classnames("shoppingContainer", { "mobile": isMobile() })}><LightBoxShopping onHideShopping={() => this.onHideShopping()} photoId={this.props.data.photoId} folderId={this.props.data.folderId} isAdmin={this.props.isAdmin} showShopping={this.showShopping} formats={this.props.formats} /></div>}
                </div>
            </Swipeable >
        )
    }
}