import Axios from "axios";
import * as settings from "./settings";
import { IApiResponse } from "./IApiResponse";
import { Form } from "reactstrap";



export class PhotoClient {
    EditPhotographer(data: any) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/EditPhotographer`, data, settings.CONFIG);
    }

    SaveSoftwareSettings(logo: File, promotion: File, mobileToken: string) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";

        let formData = new FormData();
        formData.append("logo", logo);
        formData.append("promotion", promotion);
        formData.append("mobileToken", mobileToken)

        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/SaveSoftwareSettings`, formData, settings.CONFIG);
    }

    SetPassword(password: string) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/SetPassword`, { Password: password }, settings.CONFIG);
    }

    AddProduct(data: any) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/AddProduct`, data, settings.CONFIG);
    }

    EditProduct(data: any) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/EditProduct`, data, settings.CONFIG);
    }

    EditAutogeneratedProduct(data: any) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/EditAutogeneratedProduct`, data, settings.CONFIG);
    }

    DeleteProduct(productId: number) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/DeleteProduct`, { Id: productId }, settings.CONFIG);
    }

    ListProducts() {
        return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/photo/ListProducts`, settings.CONFIG);
    }

    SetProductVisible(productId: number, visible: boolean) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/photo/SetProductVisible`, { Id: productId, Visible: visible }, settings.CONFIG);

    }
}