import React from "react";
import { IImage } from "../ImageLightbox";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// import "./ImageLightboxFooter.scss";
import { Favourites } from "../Models/Favourites";


export class ImageLightboxFooter extends React.Component<any, any> {

    state = {
        change: false
    }

    onClose = () => {
        this.props.modalProps.onClose();
    }

    toggleShopping = () => {
        if (this.props.toggleShopping) {
            this.props.toggleShopping();
        }
    }

    addToFavourite = () => {
        let data: IImage = this.props.currentView;
        if (Favourites.isFavourite(data.folderId, data.photoId)) {
            Favourites.removeItem(data.folderId, data.photoId);
        } else {
            Favourites.addItem(data.folderId, data.photoId);
        }

        this.setState({ change: !this.state.change })
    }

    isFavourite = (): boolean => {
        let data: IImage = this.props.currentView
        return Favourites.isFavourite(data.folderId, data.photoId);
    }

    render() {
        let data: IImage = this.props.currentView
        return (<div className="imageLightboxFooter">
            <div className="productButton" onClick={() => this.toggleShopping()}><img src="/Content/Images/productIcon-bold.png" /></div>
            <div className="favouriteButton" onClick={this.addToFavourite}>{!this.isFavourite() ? <FontAwesomeIcon icon={["far", "head-side"]} /> : <FontAwesomeIcon icon={["far", "head-side"]} />}</div>
        </div>)
    }
}

