import React from "react";
import { Row, Card, CardBody, CardTitle, FormGroup, Label, Input, FormText, CustomInput, Button, InputGroup, InputGroupAddon } from "reactstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { HomeClient } from "../../../api/homeClient";
import { Formik, Form, Field } from "formik";
import { PhotoClient } from "../../../api/photoClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { PhotographerClient, UserSettingsDTO } from "../../../api/ApiClient";
import { API_BASE } from "../../../api/ApiSettings";
import { AuthService } from "../../../services/auth.service";
import { BASE_URL, WEB_ORDERING_BASE_URL } from "../../../api/settings";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSpinner } from "@fortawesome/free-solid-svg-icons";

export class PhotoHostingSettings extends React.Component<any, any> {

    homeClient: HomeClient = new HomeClient();
    photoClient: PhotoClient = new PhotoClient();

    photographerClient: PhotographerClient = new PhotographerClient(API_BASE)
    authService: AuthService = new AuthService();
    state = {
        HelpVideoMobile: "",
        HelpVideoDesktop: "",
        BannerUrl: "",
        loader: 0

    }

    constructor(props) {
        super(props);

        this.homeClient.PhotographerDetails().then((response) => {
            if (response.data.Data) {
                this.setState({ MobileToken: response.data.Data.MobileToken })
            }
        });

        this.photographerClient.settings(this.authService.getId()).then((response) => {
            this.setState({ HelpVideoMobile: response.helpVideoMobile, HelpVideoDesktop: response.helpVideoDesktop, BannerUrl: response.bannerPhotoUrl });
        })
    }

    get initialValues() {
        return {
            HelpVideoMobile: this.state.HelpVideoMobile || "",
            HelpVideoDesktop: this.state.HelpVideoDesktop || "",
            BannerFile: null

        }
    }

    onSubmit = async (values, formik) => {
        console.log('submit', values);
        let videoUpdate = await this.photographerClient.edit(this.authService.getId(), values.HelpVideoDesktop, values.HelpVideoMobile, values.BannerFile != null ? { data: values.BannerFile, fileName: values.BannerFile.name } : null)


        if (videoUpdate != null) {
            NotificationManager.success("Data saved", "Data saved correctly", 3000, null, null, '');
            this.setState({ BannerUrl: videoUpdate.bannerPhotoUrl })
        }

        formik.setSubmitting(false);
        this.setState({ loader: 0 })
    }

    showLoader = (id, isSubmitting) => {
        return this.state.loader == id && isSubmitting;
    }

    showCustomBanner = () => {
        window.open(this.state.BannerUrl, '_blank', 'noopener,noreferrer')
    }

    render() {
        return (
            <>
                <Formik
                    initialValues={this.initialValues}
                    onSubmit={this.onSubmit}
                    enableReinitialize={true}
                >
                    {({
                        handleSubmit,
                        setFieldValue,
                        setFieldTouched,
                        handleChange,
                        handleBlur,
                        values,
                        errors,
                        touched,
                        isSubmitting,
                        submitForm
                    }) => (
                        <Form>
                            <Row>
                                <Colxx xxs="12">
                                    <Breadcrumb heading="Settings" match={this.props.match} />
                                    <Separator className="mb-5" />
                                </Colxx>
                            </Row>
                            <Row>
                                <Colxx xxs="12">
                                    <Card>
                                        <CardBody>
                                            <CardTitle>
                                                Software settings
                                            </CardTitle>

                                            <div className="row">
                                                <div className="col-xs-6">
                                                    <FormGroup>
                                                        <Label for="LabId">
                                                            Youtube Mobile Help Video
                                                        </Label>
                                                        <InputGroup>
                                                            <Field className="form-control" name="HelpVideoMobile" type="text" id="helpVideoMobile" placeholder={"Youtube link"} />
                                                        </InputGroup>

                                                    </FormGroup>
                                                </div>
                                                <div className="col-xs-6">
                                                    <FormGroup>
                                                        <Label for="LabId">
                                                            Youtube Desktop Help Video
                                                        </Label>
                                                        <InputGroup>
                                                            <Field className="form-control" name="HelpVideoDesktop" type="text" id="helpVideoDesktop" placeholder={"Youtube link"} />
                                                        </InputGroup>

                                                    </FormGroup>
                                                </div>
                                            </div>


                                            <Button disabled={this.showLoader(1, isSubmitting)} type="submit" color="primary" className="mt-4" onClick={() => { this.setState({ loader: 1 }) }}>
                                                {this.showLoader(1, isSubmitting) && <FontAwesomeIcon size="lg" icon={faSpinner} spin={true} style={{ color: "#fff" }} />} Save
                                            </Button>

                                        </CardBody>
                                    </Card>
                                </Colxx>
                            </Row>
                            <Row className="mt-3">
                                <Colxx xxs="12">
                                    <Card>
                                        <CardBody>
                                            <CardTitle>
                                                Banner photo
                                            </CardTitle>

                                            <FormGroup>
                                                <InputGroup>
                                                    <CustomInput
                                                        type="file"
                                                        id="promotion"
                                                        name="promotion"
                                                        onChange={(event) => {
                                                            if (event != null && event.currentTarget != null && event.currentTarget.files) {
                                                                setFieldValue("BannerFile", event.currentTarget.files[0]);
                                                            }
                                                        }} />

                                                    <InputGroupAddon onClick={(e) => this.showCustomBanner()} addonType="append" style={{ cursor: this.state.BannerUrl != "" && this.state.BannerUrl != null ? "pointer" : "default" }}>Show</InputGroupAddon>
                                                </InputGroup>
                                                <FormText color="muted">
                                                    (maximu size 1440x2560 72ppi .jpg or .png
                                                </FormText>
                                            </FormGroup>

                                            <Button disabled={this.showLoader(2, isSubmitting)} type="submit" color="primary" className="mt-1" onClick={() => { this.setState({ loader: 2 }) }}>
                                                {this.showLoader(2, isSubmitting) && <FontAwesomeIcon size="lg" icon={faSpinner} spin={true} style={{ color: "#fff" }} />} Save
                                            </Button>

                                        </CardBody>
                                    </Card>
                                </Colxx>
                            </Row>
                            <Row className="mt-3">
                                <Colxx xxs="12">
                                    <Card>
                                        <CardBody>
                                            <CardTitle>
                                                Direct link to your gallery
                                            </CardTitle>

                                            <div className="row">
                                                <div className="col-xs-12">
                                                    <a href={`${BASE_URL}/Event/List/${this.authService.getId()}`} target="_blank">{`${BASE_URL}/Event/List/${this.authService.getId()}`}</a>
                                                </div>
                                            </div>

                                        </CardBody>
                                    </Card>
                                </Colxx>
                            </Row>

                        </Form>
                    )}
                </Formik>
            </>
        );
    }
}

export default PhotoHostingSettings;