import React from "react";
import { Row, Card, CardTitle, Form, FormGroup, Label, Button } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { Formik, Field } from "formik";
import { Redirect } from "react-router-dom";

import "./DirectEventView.scss";
import { EventClient } from "../../../api/eventsClient";
import { BASE_URL } from "../../../api/settings";

export class DirectEventView extends React.Component<any, any> {

    eventClient: EventClient;

    constructor(props: any) {
        super(props);

        this.state = {
            currentState: "check",
            eventId: 0,
            redirect: ""
        }

        this.eventClient = new EventClient();
    }

    componentDidMount() {
        console.log(this.props);
        this.eventClient.EventNoPassword(this.props.match.params.eventGuid).then((response) => {
            if (response.data.Data != null && response.data.Error == null) {
                this.setState({ currentState: "success", eventId: response.data.Data });
                setTimeout(() => {
                    window.location.replace(`${BASE_URL}/EventShow/${this.props.match.params.eventGuid}`);
                    //this.setState({ redirect: `${BASE_URL}/EventShow/${response.data.Data}` })
                }, 3000);
            } else {
                this.setState({ currentState: "error" });
            }
        });
    }

    render() {
        if (this.state.redirect) {
            return (<Redirect to={this.state.redirect} />)
        }
        return (<><Row style={{ height: "100vh" }} className="directEventView">
            <Colxx xxs="12" md="4" className="mx-auto my-auto">
                <Card className="auth-card">

                    <div className="form-side w-100 d-flex justify-content-center align-items-center">
                        <CardTitle className="mb-4 text-center">
                            {this.state.currentState == "check" && <div className="loading" />}
                            {this.state.currentState == "error" && <div className="glyph-icon simple-icon-close text-danger" style={{ fontSize: 29 }}></div>}
                            {this.state.currentState == "success" && <div className="glyph-icon simple-icon-check text-success" style={{ fontSize: 29 }}></div>}
                            {this.state.currentState == "check" && <div className="mt-4">Checking event access...</div>}
                            {this.state.currentState == "error" && <div className="mt-4">Access not granted</div>}
                            {this.state.currentState == "success" && <div className="mt-4">Access granted. Redirecting to event...</div>}
                        </CardTitle>
                    </div>
                </Card>
            </Colxx>
        </Row>
        </>);
    }
}

export default DirectEventView;