import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import AppLayout from '../../layout/AppLayout';
import PhotosView from './events/photos/photosView';
import AuthRoute from "../../components/authRoute";
import PhotoHostingSettings from './settings';

const DefaultView = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './events/index')
);

const OverviewView = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './overview')
);

const UpdatesView = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './updates')
);

const SubfolderView = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './events/subfolders')
);

const Dashboard = (props) => {
  return (<AppLayout>
    <div className="photohosting-wrapper">
      <Suspense fallback={<div className="loading" />}>
        <Switch>
          <AuthRoute authUser={props.authUser} path={`${props.match.url}/overview`} component={OverviewView} />
          <AuthRoute authUser={props.authUser} path={`${props.match.url}/updates`} component={UpdatesView} />
          <AuthRoute authUser={props.authUser} path={`${props.match.url}/settings`} component={PhotoHostingSettings} />
          <Route path={`${props.match.url}/event/:eventId/:folderId/:page?`} render={(props) => <PhotosView {...props} />} />
          <Route path={`${props.match.url}/event/:eventId`} render={(props) => <SubfolderView {...props} />} />
          <Route path={`${props.match.url}/:photographerId?`} render={(props) => <DefaultView {...props} />} />
          <Route path={`${props.match.url}`} render={(props) => <DefaultView {...props} />} />
          <Redirect to="/error" />
        </Switch>
      </Suspense>
    </div>
  </AppLayout>)

}
export default Dashboard;
