import React from "react";
import { EventClient } from "../../../api/eventsClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { Row } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { ParallaxItem } from "../../../components/parallax/parallaxItem";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { Redirect } from "react-router-dom";
import { EventFolderPhotos } from "../folder/EventFolderPhotos/EventFolderPhotos";
import { ClientLayoutHeader } from "../../../layout/ClientLayoutHeader";

export class Favourites extends React.Component<any, any> {

    state = {
        redirect: "",
        response: null,
        page: 0
    }

    eventClient: EventClient;

    constructor(props: any) {
        super(props);
        console.log(props);
        this.eventClient = new EventClient();
    }

    componentDidMount() {
        this.populateData();
    }

    populateData() {
        this.setState({ response: null });

        this.eventClient.EventFolderPhotosFavourites2(this.props.match.params.folderId).then(response => {
            if (response.data.Redirect) {
                this.setState({ redirect: response.data.Redirect });
            } else
                if (response.data.Error) {
                    NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
                } else {
                    if (response.data.Data != null) {
                        this.setState({
                            currentPage: response.data.Data.Page,
                            totalPage: response.data.Data.PageCount,
                            eventData: response.data.Data,
                            response: response.data.Data
                        });
                    }
                }
        });
    }

    public componentDidUpdate(prevProps) {
        if (this.props.location.pathname != prevProps.location.pathname) {
            this.setState({ redirect: "" });
            this.populateData();
        }
    }



    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: (this.state.response as any).EventCoverPhotoUrl,
            Top: (this.state.response as any).CoverOffset ? (this.state.response as any).CoverOffset : 0,
            ObjectId: Number((this.state.response as any).EventId),
            Mode: "folder",
            CanEdit: false
        }
    }

    get folderTitle() {
        if (this.state.response && (this.state.response as any).eventFolderList) {
            return (this.state.response as any).CurrentFolderName;
        }
        return "";
    }

    changeFolder = (next: boolean) => {

        this.eventClient.ChangeFolder(this.props.match.params.folderId, next).then((response) => {
            this.setState({ redirect: response.data.Redirect });
        });
    }

    render() {

        if (this.state.redirect) {
            return (<Redirect to={this.state.redirect} />);
        }

        return (<>
            <ClientLayoutHeader backUrl={`/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}`} checkoutUrl={`/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}/checkout`} favouriteUrl={`/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}`} favouriteText="All photos" imageIcon={true} />

            <Row style={{ marginBottom: "18px" }}>
                <Colxx className={"p-0"} xxs="12">
                    {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                </Colxx>
            </Row>
            <Row>
                <Colxx xxs="12" className="text-center">
                    {this.folderTitle && <h1><span className="mr-4" style={{ cursor: "pointer" }} onClick={() => this.changeFolder(false)}><FontAwesomeIcon icon={faAngleLeft} /></span>{this.folderTitle}<span onClick={() => this.changeFolder(true)} className="ml-4" style={{ cursor: "pointer" }}><FontAwesomeIcon icon={faAngleRight} /></span></h1>}
                </Colxx>
            </Row>

            {
                this.state.response != null && <EventFolderPhotos
                    canLoad={false}
                    page={this.state.page}
                    eventId={(this.state.response as any).EventId}
                    photoCount={(this.state.response as any).photosCount}
                    folderId={(this.state.response as any).Id}
                    formats={(this.state.response as any).FormatInfo}
                    isAdmin={false}
                    folder={(this.state.response as any).CurrentFolderName}
                    data={(this.state.response as any).PhotoList}
                    favouritesOnly={true}
                    formatFilter={null}
                    foldersIds={(this.state.response as any).PhotoList.map(x => x.FolderId).filter((value, index, self) => { return self.indexOf(value) === index; })}
                />
            }
        </>
        );
    }
}

export default Favourites;