import React, { Suspense } from 'react';
import { Route, Switch, Redirect } from 'react-router-dom';
import { ClientLayout } from '../../layout/ClientLayout';


const ViewEvent = React.lazy(() =>
    import(/* webpackChunkName: "views" */ './event/Event')
);

const ViewEventLogin = React.lazy(() =>
    import(/* webpackChunkName: "views" */ './eventlogin/EventLogin')
);

const ViewEventFolder = React.lazy(() => import(/* webpackChunkName: "views" */ './folder/Folder'));

const ViewFavourites = React.lazy(() => import(/* webpackChunkName: "views" */ './favourites/Favourites'));

const ViewCheckout = React.lazy(() => import(/* webpackChunkName: "views" */ './checkout/Checkout'));

const ViewDirectEvent = React.lazy(() => import(/* webpackChunkName: "views" */ './direct/DirectEventView'));

const ViewPhotographer = React.lazy(() => import(/* webpackChunkName: "views" */ './photographer/PhotographerView'));

const ViewNewAccount = React.lazy(() => import(/* webpackChunkName: "views" */ './newaccount/NewAccountView'));

export class Portal extends React.Component<any, any> {
    render() {
        return (<ClientLayout>
            <Suspense fallback={<div className="loading" />}>
                <Switch>
                    <Route exact path={`${this.props.match.url}`} render={(props) => <ViewEventLogin {...props} />} />
                    <Route exact path={`${this.props.match.url}/photographer/:photographerId`} render={(props) => <ViewPhotographer {...props} />} />
                    <Route exact path={`${this.props.match.url}/event/:eventId`} render={(props) => <ViewEvent {...props} />} />

                    <Route exact path={`${this.props.match.url}/event/:eventId/folder/:folderId/favourites`} render={(props) => <ViewFavourites {...props} />} />
                    <Route exact path={`${this.props.match.url}/event/:eventId/folder/:folderId/checkout`} render={(props) => <ViewCheckout {...props} />} />
                    <Route exact path={`${this.props.match.url}/event/:eventId/folder/:folderId/checkout/format/:order`} render={(props) => <ViewCheckout {...props} />} />
                    <Route exact path={`${this.props.match.url}/event/:eventId/folder/:folderId/:page?`} render={(props) => <ViewEventFolder {...props} />} />
                    <Route exact path={`${this.props.match.url}/direct/:eventGuid?`} render={(props) => <ViewDirectEvent {...props} />} />
                    <Route exact path={`${this.props.match.url}/newaccount`} render={(props) => <ViewNewAccount {...props} />} />
                    <Redirect to="/error" />
                </Switch>
            </Suspense>
        </ClientLayout>);
    }
}

export default Portal;
