import React from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button, FormGroup, Label } from "reactstrap";
import { Formik, Form, Field, FormikHelpers, ErrorMessage, } from "formik";
import * as Yup from "yup";

export interface ICheckoutFormProps {
    toggle: (state: boolean) => void,
    onSubmit: (data: any, actions: FormikHelpers<any>) => void
    isOpen: boolean
}

const CheckoutFormSchema = Yup.object().shape({
    ClientFirstName: Yup.string().required('Field is required'),
    ClientLastName: Yup.string().required('Field is required'),
    PhoneNumber: Yup.string().required('Field is required'),
    Email: Yup.string().email('Invalid email').required('Field is required'),
});

const initialState = {
    ClientFirstName: "",
    ClientLastName: "",
    PhoneNumber: "",
    Email: "",
    ExtraInformation: ""
}

export class CheckoutForm extends React.Component<ICheckoutFormProps, any> {

    onSubmit = (values, actions: FormikHelpers<any>) => {
        this.props.onSubmit(values, actions)
    }

    render() {
        return (<Modal
            isOpen={this.props.isOpen}
            size="lg"
            toggle={() => this.props.toggle(!this.props.isOpen)}
        >
            <Formik initialValues={initialState}
                onSubmit={this.onSubmit} validationSchema={CheckoutFormSchema}>
                {({
                    handleSubmit,
                    setFieldValue,
                    setFieldTouched,
                    values,
                    errors,
                    touched,
                    isSubmitting,
                }) => (
                        <Form>
                            <ModalHeader>Send Order</ModalHeader>
                            <ModalBody>
                                <FormGroup>
                                    <Label for="ClientFirstName">
                                         *
                                </Label>
                                    <Field className="form-control" name="ClientFirstName" type="text" id="ClientFirstName" />
                                    {errors.ClientFirstName && touched.ClientFirstName ? (
                                        <div className="invalid-feedback d-block">
                                            {errors.ClientFirstName}
                                        </div>
                                    ) : null}
                                </FormGroup>
                                <FormGroup>
                                    <Label for="ClientLastName">
                                         *
                                </Label>
                                    <Field className="form-control" name="ClientLastName" type="text" id="ClientLastName" />
                                    {errors.ClientLastName && touched.ClientLastName ? (
                                        <div className="invalid-feedback d-block">
                                            {errors.ClientLastName}
                                        </div>
                                    ) : null}
                                </FormGroup>
                                <FormGroup>
                                    <Label for="PhoneNumber">
                                          *
                                </Label>
                                    <Field className="form-control" name="PhoneNumber" type="text" id="PhoneNumber" />
                                    {errors.PhoneNumber && touched.PhoneNumber ? (
                                        <div className="invalid-feedback d-block">
                                            {errors.PhoneNumber}
                                        </div>
                                    ) : null}
                                </FormGroup>
                                <FormGroup>
                                    <Label for="Email">
                                        E-mail *
                                </Label>
                                    <Field className="form-control" name="Email" type="text" id="Email" />
                                    {errors.Email && touched.Email ? (
                                        <div className="invalid-feedback d-block">
                                            {errors.Email}
                                        </div>
                                    ) : null}
                                </FormGroup>
                                <FormGroup>
                                    <Label for="ExtraInformation">
                                          
                                </Label>
                                    <Field className="form-control" name="ExtraInformation" type="textarea" id="ExtraInformation" component="textarea" />
                                </FormGroup>
                                {isSubmitting && <div className={"w-100 h-100"} style={{ position: "absolute", top: 0, left: 0, display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "rgba(255,255,255,0.6)" }}><div className="loading" style={{ left: "auto", top: "auto" }} /></div>}
                            </ModalBody>
                            <ModalFooter>
                                <Button
                                    type="submit"
                                    color="primary"
                                >

                                    Send
                    </Button>{' '}
                                <Button
                                    color="secondary"
                                    onClick={() => this.props.toggle(false)}
                                >
                                    Cancel
                    </Button>
                            </ModalFooter>

                        </Form>)}

            </Formik>
        </Modal>)
    }

}