
import * as React from "react";
import { Step1 } from "./GalleryUploaderStep1";
import { Step2 } from "./GalleryUploaderStep2";
import { Step3 } from "./GalleryUploaderStep3";
import { Step4 } from "./GalleryUploaderStep4";
import axios from "axios";
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from "reactstrap";
import { BASE_URL, CONFIG } from "../../api/settings";

enum WatermarkType { Horizontal, Vertical }

export interface GalleyUploaderState {
    show: boolean,
    step: number,
    data: {
        watermarkHorizontal: Blob | null,
        watermarkVertical: Blob | null,
        files: FileList | null
    }
}

export interface GalleryUploadProps {
    folderId: number,
    toggleModal: () => void,
    modalOpen: boolean,
    onUploadCompleted: () => void

}

export default class GalleryUplodader extends React.Component<GalleryUploadProps, GalleyUploaderState> {

    hiddenFileInputRef: any = React.createRef();

    constructor(props: GalleryUploadProps) {
        super(props);
        this.state = {
            show: false,
            step: 1,
            data: {
                watermarkHorizontal: null,
                watermarkVertical: null,
                files: null
            }
        }
    }

    show = (show: boolean) => {
        this.setState({ show });
    }

    close = () => {
        this.setState({ show: false, step: 1 });
    }

    optionOneClick = () => {
        (document as any).getElementById("hiddenfile").click()
    }

    onChangeOptionOneInput = (e: React.ChangeEvent<HTMLInputElement>) => {
        let fileNames: string[] = [];
        let fileSizes: number[] = [];

        for (let i = 0; i < (e.target.files as FileList).length; i++) {
            fileSizes[i] = (e.target.files as FileList)[i].size;
            fileNames[i] = (e.target.files as FileList)[i].name;
        }

        axios.post(BASE_URL + '/Event/SaveFolderPhotos', { fileNames: fileNames, fileSizes: fileSizes, folderId: Number(this.props.folderId) }, {
            withCredentials: true,
            headers: {
                "Content-Type": 'application/json',
                "X-Requested-With": "XMLHttpRequest",
            }
        }).then(response => {
            this.onUploadCompleted();
        });
    }

    optionTwoClick = () => {
        this.setState({ step: 2 });
    }

    switchHeader = () => {
        switch (this.state.step) {
            case 1: return "First step";
            case 2: return "Second step";
            case 3: return "Third step";
            case 4: return "Fourth step";
        }
    }

    watermarkChange = (image: Blob, type: WatermarkType) => {
        if (type === WatermarkType.Horizontal) {
            this.setState(prevState => ({
                ...prevState,
                data: {
                    ...prevState.data,
                    watermarkHorizontal: image
                }
            }));
        } else if (type === WatermarkType.Vertical) {
            this.setState(prevState => ({
                ...prevState,
                data: {
                    ...prevState.data,
                    watermarkVertical: image
                }
            }));
        }
    }

    showNextButton = () => {
        return this.state.step == 2;
    }

    nextState = () => {
        this.setState((prevState) => ({
            ...prevState,
            step: prevState.step + 1
        }));
    }

    onSelectedFiles = (files: FileList) => {
        this.setState(prevState => ({
            ...prevState,
            data: {
                ...prevState.data,
                files: files
            },
            step: prevState.step + 1
        }));
    }

    onUploadCompleted = () => {
        setTimeout(() => {
            this.props.onUploadCompleted();
            this.setState({ step: 1 })
        }, 3000);
    }

    onCancel = () => {
        this.props.toggleModal();
        this.setState({ step: 1 });
    }


    render() {
        return (
            <div className="galleryUploader">
                <Modal
                    isOpen={this.props.modalOpen}
                    toggle={this.onCancel}
                    wrapClassName="modal-right"
                    backdrop="static"
                >
                    <ModalHeader>
                        {this.switchHeader()}
                    </ModalHeader>
                    <ModalBody>
                        <input hidden={true} id="hiddenfile" accept="image/*" multiple={true} type="file" onInput={this.onChangeOptionOneInput} />
                        {this.state.step == 1 && <Step1 optionOneClick={this.optionOneClick} optionTwoClick={this.optionTwoClick} />}
                        {this.state.step == 2 && <Step2 watermarkHorizontalChange={(image) => this.watermarkChange(image, WatermarkType.Horizontal)} watermarkVerticalChange={(image) => this.watermarkChange(image, WatermarkType.Vertical)} />}
                        {this.state.step == 3 && <Step3 onUpload={this.onSelectedFiles} />}
                        {this.state.step == 4 && <Step4 onUploadCompleted={this.onUploadCompleted} folderId={this.props.folderId} autostart={true} files={this.state.data.files as FileList} watermarkVertical={this.state.data.watermarkVertical as Blob} watermarkHorizontal={this.state.data.watermarkHorizontal as Blob} />}
                    </ModalBody>
                    <ModalFooter>
                        <Button color="secondary" outline onClick={this.onCancel}>
                            Cancel
                        </Button>
                        {this.showNextButton() && <Button onClick={this.nextState} bsStyle="primary">Next</Button>}
                    </ModalFooter>
                </Modal>
            </div>
        );
    }

}