import React from "react";
import classnames from "classnames";
import "animate.css/animate.min.css";
import "./LightBoxShopping.scss"
import { IFormat } from "../Models/Format";
import { ShoppingCart } from "../Models/ShoppingCart";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPlus, faMinus } from '@fortawesome/free-solid-svg-icons'

export interface ILightBoxShopping {
    formats: IFormat[],
    isAdmin: boolean,
    showShopping: boolean,
    photoId: number,
    folderId: number,
    onHideShopping: () => void
}

export interface ILightBoxShoppingState {
    data: { [key: number]: number };
    showShopping: boolean
}

export class LightBoxShopping extends React.Component<ILightBoxShopping, ILightBoxShoppingState> {
    state = {
        data: {},
        showShopping: false
    }


    constructor(props: ILightBoxShopping) {
        super(props);
        this.state.data = {};
        let sc = new ShoppingCart(this.props.folderId);
        let shoppingCartItem = sc.getItem(props.photoId);
        props.formats.forEach((format, index) => {
            this.state.data[index] = 0
            if (shoppingCartItem != null) {
                this.state.data[index] = shoppingCartItem.getFormatCount(index);
            }
        })
    }


    onChangeValue = (index: any, e: React.ChangeEvent<HTMLInputElement>) => {
        let value = e.target.valueAsNumber;

        if (this.state.data[index] == null || value < 0) {
            return;
        }

        this.setState(prevState => {
            ShoppingCart.addItem(this.props.folderId, this.props.photoId, index, value);
            return {
                ...prevState,
                data: {
                    ...prevState.data,
                    [index]: value
                }
            }
        });
    }

    onChangeByButton = (index: number, value: number) => {
        if (this.state.data[index] == null || this.state.data[index] + value < 0) {
            return;
        }

        this.setState(prevState => {
            ShoppingCart.addItem(this.props.folderId, this.props.photoId, index, this.state.data[index] + value);
            return {
                ...prevState,
                data: {
                    ...prevState.data,
                    [index]: prevState.data[index] + value
                }
            }
        });
    }

    hideShopping = () => {
        if (this.props.onHideShopping) {
            this.props.onHideShopping();
        }
    }

    get showShopping(): boolean {
        return this.props.showShopping || this.state.showShopping;
    }
    isEmptyOrSpaces = (str) => {
        return str === null || str.match(/^ *$/) !== null;
    }


    render() {
        return (
            <div className={classnames("lightBoxShopping", { "showed": this.showShopping })}>
                {this.showShopping && <React.Fragment>

                    <div className="background"></div>
                    <div className="header" onClick={() => this.hideShopping()}><img src="/Content/Images/productIcon-normal.png" />
                        <div>Hide products</div>
                    </div>
                    {this.props.formats.map((format, index) => {
                        if (this.isEmptyOrSpaces(format.Type)) {
                            return (<div></div>);
                        } else {
                            return (<div className="formatData" key={"formatData" + index}>
                                <div className="formatName">{format.Type}</div>
                                <div className="formatButtons"><div className="formatButton" onClick={() => this.onChangeByButton(index, -1)}><FontAwesomeIcon icon={faMinus} /></div><div>
                                    <input onKeyDownCapture={(e) => e.stopPropagation()} value={this.state.data[index]} className="formatCount" type="number" onChange={e => this.onChangeValue(index, e)} /></div><div className="formatButton" onClick={() => this.onChangeByButton(index, 1)}><FontAwesomeIcon icon={faPlus} /></div></div>
                            </div>);
                        }

                    })}
                </React.Fragment>}
            </div>
        )
    }
}