/* eslint-disable */

import React from "react";
import Gallery, { RenderImageProps } from "react-photo-gallery";
import { ImageData } from "./Models/Image";
import axios from "axios";
import { ImageSizer } from "../../../../components/ImageSizer";
import { IsMobile } from "./Helpers/IsMobileHelper"
import ReactPaginate from "react-paginate";
import "./EventFolderPhotos.scss";
import { ImageLightbox, IImage } from "./ImageLightbox";
import { IFormat } from "./Models/Format";
import { IPersonalData, ShoppingCart, OrderShoppingCart, IShoppingCart } from "./Models/ShoppingCart";
import { Favourites, OrderFavourites } from "./Models/Favourites";
import classnames from "classnames"
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons'


export interface IEventFolderPhotosState {
    sizes: { [key: number]: { width: number, height: number } },
    data: ImageData[],
    lightbox: {
        isOpen: boolean,
        currentIndex: number
    },
    loading: boolean,
    targetRowHeight: number,
    isMobile: boolean,
    prevScrollpos: number,
    visiblePagination: boolean,
}

export enum MenuType { Buy = 0, FormatInfo = 1 }

export interface IEventFolderPhotosProps {
    data: ImageData[],
    isAdmin: boolean,
    folder: string,
    formats: IFormat[],
    folderId: number,
    photoCount: number
    eventId: number,
    canLoad: boolean,
    formatFilter: number | null,
    favouritesOnly: boolean | null,
    foldersIds: number[] | null,
    page: number,
    onPageChange?: (page: number) => void
}



export class EventFolderPhotos extends React.Component<IEventFolderPhotosProps, IEventFolderPhotosState> {

    photoContainerRef = React.createRef<HTMLDivElement>();


    constructor(props: IEventFolderPhotosProps) {
        super(props);
        console.log(props);
        let shoppingCart = this.getShoppingCart();
        this.state = {
            sizes: {},
            data: props.formatFilter != null ? this.props.data.filter(image => shoppingCart.hasFormat(image.Id, props.formatFilter as number)) : this.props.data,
            lightbox: {
                isOpen: false,
                currentIndex: 0
            },
            loading: false,
            targetRowHeight: this.getTargetRowHeight(),
            isMobile: IsMobile(),
            prevScrollpos: window.pageYOffset,
            visiblePagination: false
        }
    }

    getShoppingCart = (): IShoppingCart => {
        let shoppingCart: IShoppingCart;
        if (this.props.foldersIds != null && this.props.foldersIds.length > 0) {
            shoppingCart = new OrderShoppingCart(this.props.foldersIds);
        } else {
            shoppingCart = new ShoppingCart(this.props.folderId);
        }
        return shoppingCart;
    }

    get stateImages() {
        if (this.props.favouritesOnly) {
            if (this.props.foldersIds) {
                let favourite = new OrderFavourites(this.props.foldersIds);
                return this.state.data.filter(d => favourite.isFavourite(d.Id));
            }
            return [];



        } else {
            return this.state.data;
        }
    }

    getTargetRowHeight() {
        let width = window.innerWidth;
        let targetRowHeight = 0;
        if (width > 600) {
            targetRowHeight = 250;
        } else if (width <= 600 && width > 360) {
            targetRowHeight = 150;
        } else {
            targetRowHeight = 100;
        }
        return targetRowHeight;
    }

    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ isMobile: IsMobile() })
        });

        window.addEventListener("scroll", this.handleScroll);
    }

    handleScroll = () => {
        const { prevScrollpos } = this.state;

        const currentScrollPos = window.pageYOffset;
        const visible = prevScrollpos > currentScrollPos;


        const winScroll =
            document.body.scrollTop || document.documentElement.scrollTop

        const height =
            document.documentElement.scrollHeight -
            document.documentElement.clientHeight

        const scrolled = winScroll / height


        this.setState({
            prevScrollpos: currentScrollPos,
            visiblePagination: (visible || scrolled > 0.95)
        });
    };

    componentWillUnmount() {
        window.removeEventListener("resize", () => { });
        window.removeEventListener("scroll", this.handleScroll);
    }

    getImageProportion(naturalWidth: number, naturalHeight: number) {
        let data: number[] = [naturalWidth, naturalHeight];
        let max = Math.max(...data);
        let min = Math.min(...data);

        return max / min;

    }

    onLoadImage = (imageId: number, naturalWidth: number, naturalHeight) => {

        let width = 3;
        let height = 2;

        if (this.getImageProportion(naturalWidth, naturalHeight) == 1.5) {
            if (naturalWidth < naturalHeight) {
                height = 3;
                width = 2;
            }
        } else {
            height = 200;
            width = this.calculateAspectRatioHeight(naturalWidth, naturalHeight, 200);

        }

        this.setState((prevState) => {
            return {
                ...prevState,
                sizes: {
                    ...prevState.sizes,
                    [imageId]: {
                        width: width,
                        height: height
                    }
                }
            }
        });
    }

    get photos(): { src: string, width: number, height: number }[] {
        if (this.stateImages == null || this.stateImages.length == 0) {
            return [];
        }
        let data = this.stateImages.filter(image => this.state.sizes[image.Id] != null).map(data => { return { ...data, src: data.UrlThumbnail, width: this.state.sizes[data.Id].width, height: this.state.sizes[data.Id].height } });
        if (data.length % 2 == 1) {
            let filler = Object.assign({ isFiller: true }, data[data.length - 1]);
            data.push(filler);
        }
        return data;
    }

    sendOrder = (data: IPersonalData, callback: (response) => void) => {
        let shoppingCart = this.getShoppingCart();
        shoppingCart.submitOrder(shoppingCart.getOrder(data, this.props.data), this.props.eventId);
    }

    public sendOrder2 = (data: IPersonalData) => {
        let shoppingCart = this.getShoppingCart();
        return shoppingCart.submitOrder2(shoppingCart.getOrder(data, this.props.data), this.props.eventId);
    }

    calculateAspectRatioHeight = (srcWidth, srcHeight, maxHeight) => {
        return srcWidth * (maxHeight / srcHeight);
    }

    calculateAspectRatioWidth = (srcWidth, srcHeight, maxWidth) => {
        return srcHeight * (maxWidth / srcWidth);
    }

    isStandardAspectRatio = (srcWidth: number, srcHeight: number) => {
        if (srcWidth / srcHeight == 1.5 || (srcHeight / srcWidth).toFixed(2) == "0.67") {
            return true;
        } else {
            return false;
        }
    }


    getPhotoWidth(index: number): number {
        let width = (this.photoContainerRef.current as HTMLDivElement).offsetWidth - 20;

        if (index % 2 == 0) {
            let img1 = this.photos[index];
            let img2 = this.photos[index + 1] ? this.photos[index + 1] : this.photos[index];
            let scale = img1.width / (img1.width + img2.width);
            return width * scale;
        } else {
            let img1 = this.photos[index - 1];
            let img2 = this.photos[index];
            let scale = img2.width / (img1.width + img2.width);
            return width * scale;
        }
    }

    imageRenderer = (props: RenderImageProps) => {
        let imgStyle: any = { margin: props.margin, display: 'block' };
        if (props.direction === 'column') {
            imgStyle.position = 'absolute';
            imgStyle.left = props.left;
            imgStyle.top = props.top;
        }

        if (this.stateImages.length > 3 || window.innerWidth < 665) {
            imgStyle.width = props.photo.width;
            imgStyle.height = props.photo.height;
        } else {
            imgStyle.width = this.calculateAspectRatioHeight(props.photo.width, props.photo.height, 200);
            imgStyle.height = 200;
        }

        let filler: boolean = (props.photo as any).isFiller
        let shoppingCart = this.getShoppingCart();

        let favourite
        if (this.props.foldersIds) {
            favourite = new OrderFavourites(this.props.foldersIds);
        } else {
            favourite = new Favourites(this.props.folderId);
        }



        let photo = shoppingCart.getItem((props.photo as any).Id);
        return (<div className={"photoImage" + (IsMobile() ? " hovered" : "")} style={imgStyle} key={"photoImage-" + props.index} onClick={(e) => { !filler ? this.openLightbox(props.index, false) : null }}>
            {!filler && <img src={props.photo.src} style={{ width: "100%", height: "100%" }} />}
            {this.props.isAdmin && !filler && <div className="adminMenu" onClick={(event) => this.deleteImage(props.index, event)}><div className="adminMenuBackground"></div><i className="far fa-trash-alt"></i></div>}
            {!this.props.isAdmin && !filler && <div className="menu">
                <div className="menuBackground"></div>
                <div className="menuItem leftMenu" onClick={(e) => this.addToFavourite((props.photo as any).Id, (props.photo as any).FolderId, e)}>
                    {!favourite.isFavourite((props.photo as any).Id) ? <img className="heartIcon" src="/Content/Images/heart-o.png" /> : <img className="heartIcon" src="/Content/Images/heart.png" />}
                </div>
                {this.props.formatFilter == null && <div className="menuItem" onClick={() => this.openLightbox(props.index, true)}>
                    {photo != null && photo.hasAnyFormat() ? <img className="cartIcon" src="/Content/Images/shoppingCart-full.png" /> : <img className="cartIcon" src="/Content/Images/shoppingCart-empty.png" />}
                </div>
                }
                {this.props.formatFilter != null && <div className="menuFormat" onClick={() => this.openLightbox(props.index, true)}>
                    <div className="format">{photo.getFormatCount(this.props.formatFilter)}</div>
                </div>}
            </div>}
        </div>);
    }

    get loaderHasMore() {
        return this.props.canLoad && this.state.data.length < this.props.photoCount && !this.state.loading;
    }

    get imagesForLightBox(): IImage[] {
        return this.stateImages.map<IImage>(img => { return { src: img.Url, folder: this.props.folder, filename: img.Name, folderId: img.FolderId, photoId: img.Id } });
    }

    addToFavourite = (photoId: number, folderId: number, e: React.MouseEvent<HTMLDivElement>) => {
        e.stopPropagation();
        if (Favourites.isFavourite(folderId, photoId)) {
            Favourites.removeItem(folderId, photoId);
        } else {
            Favourites.addItem(folderId, photoId);
        }
        this.setState(this.state);
    }

    loadMore = (p) => {
        this.setState({ loading: true }, () => {
            axios.post("/Event/GetEventPhotosForGallery", { folderId: this.props.folderId, page: p, PageSize: 35 }).then((response) => {
                let images: ImageData[] = response.data;
                if (images.length) {
                    this.setState(prevState => {
                        return {
                            ...prevState,
                            data: [...prevState.data, ...images],
                            loading: false
                        }
                    })
                }
            });
        });
    }

    deleteImage = (index: number, event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
        event.stopPropagation();
        axios.post("/Event/DeletePhoto", { photoId: this.stateImages[index].Id }).then((response) => {
            if (response.data == true) {
                let images = [...this.stateImages];
                images.splice(index, 1);
                this.setState({ data: images });
            }
        });
    }

    openLightbox = (index: number, buySelector: boolean) => {
        this.setState((prevState) => {
            return {
                ...prevState,
                lightbox: {
                    isOpen: true,
                    currentIndex: index
                }
            }
        });
    }

    onCloseLightbox = () => {
        this.setState({
            ...this.state, lightbox: {
                ...this.state.lightbox,
                isOpen: false
            }
        });
    }

    onPageChange = (data: { selected: number }) => {
        if (this.props.onPageChange) {
            this.props.onPageChange((data.selected + 1));
        }
    }

    pageItemsCount: number = 50;

    get pageCount() {
        return this.props.photoCount / this.pageItemsCount;
    }

    render() {
        return (<div className={"eventFolderPhotos " + (this.state.isMobile ? "eventFolderPhotosMobile" : "")} ref={this.photoContainerRef}>
            <Gallery targetRowHeight={this.state.targetRowHeight} margin={5} photos={this.photos} renderImage={this.imageRenderer} />
            <div className={classnames("paginationContainer fixed", { "animate__animated animate__slideOutDown animate__fast": !this.state.visiblePagination, "animate__animated animate__slideInUp animate__fast": this.state.visiblePagination, })}>
                <div className="background"></div>
                {this.props.photoCount > this.pageItemsCount && this.props.page != null && <ReactPaginate
                    forcePage={this.props.page - 1}
                    previousLabel={<div><FontAwesomeIcon icon={faChevronLeft} /></div>}
                    nextLabel={<div><FontAwesomeIcon icon={faChevronRight} /></div>}
                    breakLabel={'...'}
                    breakClassName={'break-me'}
                    pageCount={this.pageCount}
                    marginPagesDisplayed={this.state.isMobile ? 1 : 5}
                    pageRangeDisplayed={this.state.isMobile ? 1 : 5}
                    onPageChange={this.onPageChange}
                    containerClassName={'pagination'}
                    subContainerClassName={'pages pagination'}
                    activeClassName={'active'}
                />}
            </div>

            {this.stateImages.map((image, index) => {
                return (<ImageSizer key={"imageSizer-" + index} src={image.UrlThumbnail} onLoad={(naturalWidth, naturalHeight) => { this.onLoadImage(image.Id, naturalWidth, naturalHeight) }} />)
            })}
            <ImageLightbox formats={this.props.formats} isAdmin={this.props.isAdmin} isOpen={this.state.lightbox.isOpen} images={this.imagesForLightBox} onClose={this.onCloseLightbox} currentIndex={this.state.lightbox.currentIndex} />
        </div>)
    }
}

export default EventFolderPhotos;