import { EventClient } from "../../../api/eventsClient";
import React from "react";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { Card, CardImg, Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, CardBody, CardSubtitle } from "reactstrap";
import { NotificationManager } from "../../../components/common/react-notifications";
import { NavLink } from "react-router-dom";

export class EventItemView extends React.Component<any, any> {

    eventClient: EventClient;

    state = {
        isImage: true,
        data: {}
    }

    constructor(props: any) {
        super(props);

        this.eventClient = new EventClient();
    }


    onImageError = () => {
        this.setState({ isImage: false })
    }

    render() {
        return (<Colxx sm="6" lg="4" xl="3" className="mb-3 eventListItem" key={this.props.event.Id}>
            <NavLink to={`/portal/event/${this.props.event.Id}`} className="w-100">
                <Card onClick={() => { }}>
                    <div className="position-relative">
                        <CardImg top alt={this.props.event.Name} src={this.state.isImage ? this.props.event.FileSrc : "https://via.placeholder.com/600?text=No%20image"} onError={(e => this.onImageError())} />
                    </div>
                    <CardBody>
                        <CardSubtitle className="mb-4">
                            {this.props.event.Name}
                        </CardSubtitle>
                    </CardBody>

                </Card>
            </NavLink>

        </Colxx >)
    }
}

export default EventItemView;
