import * as React from "react";
import Carousel, { Modal, ModalGateway } from 'react-images';
import { ImageLightboxHeader } from "./Lightbox/ImageLightboxHeader";
import { IsMobile as isMobile } from './Helpers/IsMobileHelper';
import "./ImageLightbox.scss";
import { ImageLightBoxContainer } from "./Lightbox/ImageLightBoxContainer";
import { ImageLightBoxView } from "./Lightbox/ImageLightBoxView";
import { IFormat } from "./Models/Format";
import { ImageLightboxFooter } from "./Lightbox/ImageLightBoxFooter";

export interface IImage {
    src: string,
    folder: string,
    filename: string,
    folderId: number,
    photoId: number
}

export interface IImageLightboxProps {
    isOpen: boolean,
    isAdmin: boolean,
    currentIndex: number,
    images: IImage[],
    onClose: () => void,
    formats: IFormat[]
}

export interface IImageLightboxState {
    showShopping: boolean,
    isMobile: boolean
}

export class ImageLightbox extends React.Component<IImageLightboxProps, IImageLightboxState> {



    constructor(props: IImageLightboxProps) {
        super(props);
        this.state = {
            showShopping: true && !props.isAdmin && !isMobile(),
            isMobile: isMobile(),
        }
    }

    componentWillUpdate(nextProps: IImageLightboxProps) {
        if (nextProps.isOpen != this.props.isOpen && nextProps.isOpen && isMobile()) {
            this.setState({ showShopping: false })
        }
    }

    componentDidMount() {
        window.addEventListener("resize", () => {
            this.setState({ isMobile: isMobile() })
        });
    }

    componentWillUnmount() {
        window.removeEventListener("resize", () => { });
    }

    toggleModal = () => {
        if (this.props.onClose) {
            this.props.onClose();
        }
    }

    toggleShopping = () => {
        this.changeShopping(!this.state.showShopping);
    }

    changeShopping = (show: boolean) => {
        this.setState({ showShopping: show });
    }

    render() {
        let footer: any = isMobile() ? ((props) => <ImageLightboxFooter isAdmin={this.props.isAdmin} {...props} toggleShopping={this.toggleShopping} />) : null;
        return (<div key={this.state.isMobile.toString()}>
            <ModalGateway>
                {this.props.isOpen ? (
                    <Modal onClose={this.toggleModal} allowFullscreen={false} closeOnBackdropClick={false}>
                        <Carousel currentIndex={this.props.currentIndex} views={this.props.images as any} components={{ Footer: footer, Header: (props) => <ImageLightboxHeader isAdmin={this.props.isAdmin} {...props} toggleShopping={this.toggleShopping} />, Container: (props) => <ImageLightBoxContainer hasFooter={isMobile()} {...props} />, View: (props) => <ImageLightBoxView changeShopping={this.changeShopping} {...props} showShopping={this.state.showShopping} formats={this.props.formats} isAdmin={this.props.isAdmin} /> }} />
                    </Modal>
                ) : null}
            </ModalGateway>
        </div>)
    }
}