import store from "store2";
import jwt_decode from "jwt-decode";

export class AuthService {
    static key = "token";

    public isAuthenticated() {
        return store(AuthService.key) != null;
    }

    login(token: string) {
        store(AuthService.key, token);
    }

    logout() {
        store.remove(AuthService.key);
    }

    public getEmail() {
        if (this.isAuthenticated()) {
            var token: any = jwt_decode(store(AuthService.key));
            return token["http://schemas.xmlsoap.org/ws/2005/05/identity/claims/name"]
        }
        return "";
    }

    public getId() {
        if (this.isAuthenticated()) {
            var token: any = jwt_decode(store(AuthService.key));
            return token["http://schemas.microsoft.com/ws/2008/06/identity/claims/primarysid"]
        }
        return "";
    }

    public isLab() {
        if (this.isAuthenticated()) {
            var token: any = jwt_decode(store(AuthService.key));
            return token["Labolatory"] == "True"
        }
        return false;
    }
}