import React from "react";

import "./SummaryButton.scss";
import { IFormat } from "./Models/Format";
import { ShoppingCart, OrderShoppingCart, IShoppingCart } from "./Models/ShoppingCart";
import { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Button } from "reactstrap";

export class SummaryButtonFilter extends React.Component<any, any> {

    shoppingCart: IShoppingCart;

    constructor(props: any) {
        super(props);

        this.shoppingCart = new OrderShoppingCart(this.props.foldersIds as number[]);

        this.state = {
            currentSelect: this.props.formatFilter != null ? this.props.formatFilter : -1
        }
    }

    formatClick = (index: number) => {
        if (index >= 0) {
            this.props.onChangeFormat(index);
            //window.location.href = `/Event/EventOrderSummary?folderId=${this.props.folderId}&OrderBy=${index}`
        } else {
            this.props.onChangeFormat(null);
            //window.location.href = `/Event/EventOrderSummary?folderId=${this.props.folderId}`
        }
    }

    currentFormatName = () => {
        if (this.props.formatFilter != null && this.props.formats[this.props.formatFilter] != null) {
            return this.props.formats[this.props.formatFilter].Type;
        }

        return "All formats";
    }

    resetCart = (select: number) => {
        if (window.confirm("Are you sure?")) {
            this.setState({ currentSelect: select });
            this.shoppingCart.clear();
            this.props.onResetCart();
            //window.location.href = `/Event/EventOrderSummary?folderId=${this.props.folderId}`
        }
    }

    onChangeSelect = (e: React.ChangeEvent<HTMLSelectElement>) => {
        let value: number = Number(e.target.value);
        if (value == -2) {
            this.resetCart(value);
        } else if (value == -1) {
            this.setState({ currentSelect: value });
            this.formatClick(value);
        } else if (value >= 0) {
            this.setState({ currentSelect: value });
            this.formatClick(value);
        }
    }

    get filteredFormats(): { format: IFormat, index: number }[] {
        let formats: { format: IFormat, index: number }[] = [];
        this.props.formats.forEach((f, index) => {
            if (!this.isEmptyOrSpaces(f.Type)) {
                formats.push({ format: f, index });
            }
        });
        return formats;
    }

    isEmptyOrSpaces = (str) => {
        return str === null || str.match(/^ *$/) !== null;
    }

    render() {
        return (
            <React.Fragment>
                <div className="summaryButtonFilter mobile">
                    <select value={this.state.currentSelect} className="form-control" onChange={(e) => this.onChangeSelect(e)}>
                        <option value={-1}>All formats ({this.shoppingCart.allPhotoCount()} photos)</option>

                        {this.filteredFormats.map(format => {
                            return (<option key={"filterOption" + format.index} value={format.index}>{format.format.Type} ({this.shoppingCart.getFormatItemCount(format.index)} photos)</option>)
                        })}
                        <option disabled>_________</option>
                        <option value={-2}>Reset All</option>
                    </select>
                </div>
                <div className="summaryButtonFilter desktop">
                    <UncontrolledDropdown className={"d-inline-block"}>
                        <DropdownToggle caret color="primary" outline>
                            {this.currentFormatName()}
                        </DropdownToggle>
                        <DropdownMenu>
                            <DropdownItem >
                                <a onClick={() => this.formatClick(-1)}>
                                    <strong>All formats</strong>
                                    <span> ({this.shoppingCart.allPhotoCount()} photos)</span>
                                </a>
                            </DropdownItem>
                            {this.filteredFormats.map(format => {
                                return (<DropdownItem key={"linkoption" + format.index}>
                                    <a onClick={() => this.formatClick(format.index)}>
                                        <strong>{format.format.Type}</strong>
                                        <span> ({this.shoppingCart.getFormatItemCount(format.index)} photos)</span>
                                    </a>
                                </DropdownItem>);
                            })}
                        </DropdownMenu>
                    </UncontrolledDropdown>

                    {/* <button className="buttonBlock  btn btn-default dropdown-toggle" type="button" data-toggle="dropdown" style={{ width: "200px", float: "left" }}>
                        <strong>{this.currentFormatName()}</strong> <span className="caret"></span>
                    </button>
                    <ul className="dropdown-menu">
                        <li>
                            <a onClick={() => this.formatClick(-1)}>
                                <strong>All formats</strong>
                                <span> ({this.shoppingCart.allPhotoCount()} photos)</span>
                            </a>
                        </li>
                        {this.filteredFormats.map(format => {
                            return (<li key={"linkoption" + format.index}>
                                <a onClick={() => this.formatClick(format.index)}>
                                    <strong>{format.format.Type}</strong>
                                    <span> ({this.shoppingCart.getFormatItemCount(format.index)} photos)</span>
                                </a>
                            </li>);
                        })}

                    </ul> */}
                    <Button outline color="primary" style={{ width: "200px", float: "right" }} onClick={() => this.resetCart(-2)}>
                        Reset all
                    </Button>
                </div>

            </React.Fragment>
        );
    }
}

export default SummaryButtonFilter;