import React from "react";
import { Redirect } from 'react-router-dom';
import { Row, Card, NavLink, CardTitle, FormGroup, Label, Button, Input } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { Formik, Field, Form, FormikHelpers } from "formik";
import { EventClient } from "../../../api/eventsClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { HomeClient } from "../../../api/homeClient";

export class NewAccountView extends React.Component<any, any> {

    homeClient: HomeClient;


    constructor(props: any) {
        super(props);

        this.homeClient = new HomeClient();
    }


    onSubmit = (values, formik: FormikHelpers<any>) => {
        console.log(values);

        this.homeClient.Register(values).then(response => {
            if (response.data.Data == true) {
                NotificationManager.success("Account created successfully.", "Account created.", 5000, null, null, '');
                formik.resetForm();
            } else if (response.data.Error) {
                NotificationManager.error(response.data.Error, "Account not created", 5000, null, null, '');
            }
            formik.setSubmitting(false);
        }).catch(() => {
            NotificationManager.error("Something went wrong.", "Account not created", 5000, null, null, '');
            formik.setSubmitting(false);
        });


    }


    render() {
        return (<><Row style={{ height: "100vh" }}>
            <Colxx xxs="12" md="4" className="mx-auto my-auto">
                <Card className="auth-card">

                    <div className="form-side w-100">
                        <CardTitle className="mb-4">
                            New account creator
                        </CardTitle>
                        <Formik initialValues={{
                            Login: "",
                            Nickname: "",
                            CompanyName: "",
                            Phone: "",
                            Key: ""
                        }} onSubmit={this.onSubmit}>
                            {({
                                handleSubmit,
                                setFieldValue,
                                setFieldTouched,
                                values,
                                errors,
                                touched,
                                isSubmitting,
                            }) => (
                                    <Form>
                                        <FormGroup>
                                            <Label for="Login">
                                                e-mail   
                                            </Label>
                                            <Field className="form-control" name="Login" type="text" id="Login" />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="Nickname">
                                                Name
                                            </Label>
                                            <Field className="form-control" name="Nickname" type="text" id="Nickname" />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="CompanyName">
                                                Company Name
                                            </Label>
                                            <Field className="form-control" name="CompanyName" type="text" id="CompanyName" />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="Phone">
                                                Phone Number
                                            </Label>
                                            <Field className="form-control" name="Phone" type="text" id="Phone" />
                                        </FormGroup>
                                        <FormGroup>
                                            <Label for="Key">
                                                Key
                                            </Label>
                                            <Field className="form-control" name="Key" type="password" id="Key" />
                                        </FormGroup>
                                        <Button
                                            type="submit"
                                            color="primary"
                                        >

                                            Register
                                        </Button>
                                        {isSubmitting && <div className={"w-100 h-100"} style={{ position: "absolute", top: 0, left: 0, display: "flex", justifyContent: "center", alignItems: "center", backgroundColor: "rgba(255,255,255,0.6)" }}><div className="loading" style={{ left: "auto", top: "auto" }} /></div>}
                                    </Form>
                                )}

                        </Formik>
                    </div>
                </Card>
            </Colxx>
        </Row>
        </>);
    }
}

export default NewAccountView;