import * as React from "react";
import ImageHelper from "./ImageHelper";

interface Step2Props {
    watermarkHorizontalChange: (image: Blob) => void,
    watermarkVerticalChange: (image: Blob) => void
}

export class Step2 extends React.Component<Step2Props, any> {

    state = {
        preview: {
            horizontal: "/assets/img/watermark-horizontal-placeholder.png",
            vertical: "/assets/img/watermark-vertical-placeholder.png"
        }
    }

    constructor(props: Step2Props) {
        super(props);
    }

    onChangeWatermarkHorizontal = (files: FileList) => {
        if (files.length > 0) {
            ImageHelper.resizeImage(files.item(0) as File, (file) => {
                this.props.watermarkHorizontalChange(file);
                ImageHelper.blobToBase64(file, (base64Img) => {
                    let state = Object.assign({}, this.state);
                    state.preview.horizontal = base64Img;
                    this.setState(state);
                });
            }, "png", 70);
        }
    }
    onChangeWatermarkVertical = (files: FileList) => {
        if (files.length > 0) {
            ImageHelper.resizeImage(files.item(0) as File, (file) => {
                this.props.watermarkVerticalChange(file);
                ImageHelper.blobToBase64(file, (base64Img) => {
                    let state = Object.assign({}, this.state);
                    state.preview.vertical = base64Img;
                    this.setState(state);
                });
            }, "png", 70);
        }
    }

    render() {
        return (<div className="row" style={{ width: "100%" }}>
            <div className="col-xs-12">
                <div className="text-center d-flex justifu-content-center align-items-center" style={{ height: "115px", marginBottom: "10px", display: "flex", justifyContent: "center", alignItems: "center" }} >
                    <img src={this.state.preview.horizontal} style={{ maxHeight: "190px", maxWidth: "100%" }} />
                </div>
                <div className="form-group text-center" style={{ height: "60px" }}>
                    <label htmlFor="WatermarkHorizontal">Watermark horizontal</label>
                    <input accept="image/*" type="file" className="form-control" id={"WatermarkHorizontal"} onChange={(e) => this.onChangeWatermarkHorizontal(e.target.files as FileList)} />
                </div>
            </div>
            <div className="col-xs-12" style={{ marginTop: "3rem" }}>
                <div className="text-center" style={{ height: "170px", marginBottom: "10px", display: "flex", justifyContent: "center", alignItems: "center" }} >
                    <img src={this.state.preview.vertical} style={{ maxHeight: "190px", maxWidth: "100%" }} />
                </div>
                <div className="form-group text-center" style={{ height: "60px" }}>
                    <label htmlFor="WatermarkVertical">Watermark vertical</label>
                    <input accept="image/*" type="file" className="form-control" id={"WatermarkVertical"} onChange={(e) => this.onChangeWatermarkVertical(e.target.files as FileList)} />
                </div>
            </div>
        </div>);
    }
}