import * as Cookie from "js-cookie";
import store2 from "store2";

export class OrderFavourites {

    favourites: Favourites[] = [];

    constructor(folderIds: number[]) {
        folderIds.forEach(folderId => {
            this.favourites.push(new Favourites(folderId));
        });
    }

    clear() {
        this.favourites.forEach(f => {
            f.clear();
        });
    }

    isFavourite(photoId: number): boolean {
        let fav = false;
        this.favourites.forEach(favourite => {
            if (favourite.isFavourite(photoId)) {
                fav = true;
            }
        });
        return fav;
    }

}

export class Favourites {
    public folderId: number = 0;
    photos: number[] = [];

    constructor(folderId: number) {
        let cookieData = Cookie.get(`Favourites${folderId}`);
        this.folderId = folderId;
        if (cookieData) {
            try {
                var data = JSON.parse(cookieData);
                if (Array.isArray(data)) {
                    this.photos = data;
                }
            } catch (e) {

            }
        }
    }

    clear() {
        let store = store2.namespace("Favourites");
        store.remove(`Favourites${this.folderId}`);
        Cookie.remove(`Favourites${this.folderId}`);
    }

    addItem(photoId: number) {
        let existed = this.photos.find((item) => { return item == photoId });
        if (!existed) {
            this.photos.push(photoId);
            this.save();
        }
    }

    removeItem(photoId: number) {
        let existedIndex = this.photos.indexOf(photoId);
        if (existedIndex > -1) {
            this.photos.splice(existedIndex, 1);
            this.save();
        }
    }

    isFavourite(photoId: number): boolean {
        return this.photos.indexOf(photoId) > -1;
    }

    save() {
        let cookie = Cookie.withConverter({
            write: (value, name) => {
                return value;
            }
        })

        let store = store2.namespace("Favourites");
        store.set(`Favourites${this.folderId}`, this.toString());

        cookie.set(`Favourites${this.folderId}`, this.toString(), { expires: 365, })
    }

    toString(): string {
        return JSON.stringify(this.photos);
    }

    public static removeItem(folderId: number, photoId: number) {
        let sc = new Favourites(folderId);
        sc.removeItem(photoId);
        sc.save();
    }

    public static addItem(folderId: number, photoId: number) {
        let sc = new Favourites(folderId);
        sc.addItem(photoId);
        sc.save();
    }

    public static isFavourite(folderId: number, photoId: number): boolean {
        let sc = new Favourites(folderId);
        return sc.isFavourite(photoId);
    }
}
