import React from "react";
import { Row, Button, NavLink } from "reactstrap";
import { NotificationManager } from "../../../../components/common/react-notifications";
import { Colxx, Separator } from "../../../../components/common/CustomBootstrap";
import { ContextMenu, MenuItem } from "react-contextmenu";
import { connect } from "react-redux";
import { EventClient } from "../../../../api/eventsClient";
import { number, string } from "prop-types";
import Lightbox from "react-image-lightbox";
import Gallery, { RenderImageProps } from "react-photo-gallery";
import { ImageSizer } from "../../../../components/ImageSizer";
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { Redirect } from "react-router-dom";

import "./photosView.scss";
import GalleryUplodader from "../../../../components/galleryUploader/GalleryUploader";
import { ParallaxItem } from "../../../../components/parallax/parallaxItem";
import Pagination from "../../../../containers/pages/Pagination";
import { BASE_URL } from "../../../../api/settings";
import {
    Link,
} from "react-router-dom";

export interface IPhoto {
    Id: number,
    Url: string,
    UrlThumbnail: string,
    Count: number,
    IsAlbum: boolean,
    Order: any,
    Name: string,
    FileName: string,
    FileSize: number,
    FolderId: number
}

export interface IPhotosViewState {
    data: any,
    photoIndex: number,
    isOpen: boolean,
    sizes: { [key: number]: { width: number, height: number } },
    showAddModal: boolean,
    folderId: number,
    redirect: string,
    page: number,
    loading: boolean,
    previouseSubfolder: number,
    nextSubfolder: number
}

export class PhotosView extends React.Component<any, IPhotosViewState> {

    eventClient: EventClient = new EventClient();

    constructor(props) {
        super(props);

        this.state = {
            data: null,
            photoIndex: 0,
            isOpen: false,
            showAddModal: false,
            folderId: props.match.params.folderId,
            sizes: {

            },
            page: props.match.params.page,
            redirect: "",
            loading: true,
            previouseSubfolder: 0,
            nextSubfolder: 0
        }

    }

    componentDidMount() {
        this.populateData();
    }

    componentDidUpdate(prevProps) {
        if (prevProps.match.params.page != this.props.match.params.page) {
            this.setState({ redirect: "" });
            this.populateData();
        }
        if (this.props.match.params.folderId != prevProps.match.params.folderId) {
            this.populateData();
        }
    }

    onUploadCompleted = () => {
        this.setState({ showAddModal: false });
        this.populateData();
    }

    populateData = () => {
        this.setState({ loading: true, previouseSubfolder: 0, nextSubfolder: 0 })

        this.eventClient.EventFoldersList(this.props.match.params.eventId).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                if (response.data.Data != null) {
                    var folders = response.data.Data.eventFolderList.Folders;
                    if (folders.length == 1) {
                        this.setState({
                            ...this.state,
                            nextSubfolder: this.props.match.params.folderId,
                            previouseSubfolder: this.props.match.params.folderId,
                        })
                    }

                    var currentIndex = folders.findIndex(x => x.FolderId == this.props.match.params.folderId);
                    var nextIntex = (currentIndex + 1) % folders.length;
                    var previouseIndex = currentIndex == 0 ? (folders.length - 1) : (currentIndex - 1);
                    this.setState({
                        ...this.state,
                        nextSubfolder: folders[nextIntex].FolderId,
                        previouseSubfolder: folders[previouseIndex].FolderId
                    });

                }
            }

        });

        this.eventClient.EventFolderPhotos(this.props.match.params.folderId, this.paginationOptions.page).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                if (response.data.Data != null) {
                    this.setState({
                        data: response.data.Data
                    });
                }
            }
            this.setState({ loading: false })
        });
    }

    getImageProportion(naturalWidth: number, naturalHeight: number) {
        let data: number[] = [naturalWidth, naturalHeight];
        let max = Math.max(...data);
        let min = Math.min(...data);

        return max / min;

    }

    calculateAspectRatioHeight = (srcWidth, srcHeight, maxHeight) => {
        return srcWidth * (maxHeight / srcHeight);
    }

    onLoadImage = (imageId: number, naturalWidth: number, naturalHeight) => {
        let width = 3;
        let height = 2;

        if (this.getImageProportion(naturalWidth, naturalHeight) == 1.5) {
            if (naturalWidth < naturalHeight) {
                height = 3;
                width = 2;
            }
        } else {
            height = 200;
            width = this.calculateAspectRatioHeight(naturalWidth, naturalHeight, 200);

        }
        this.setState((prevState) => {
            return {
                ...prevState,
                sizes: {
                    ...prevState.sizes,
                    [imageId]: {
                        width: width,
                        height: height
                    }
                }
            }
        });
    }

    imageRenderer = (props: RenderImageProps) => {
        let imgStyle: any = { margin: props.margin, display: 'block' };
        if (props.direction === 'column') {
            imgStyle.position = 'absolute';
            imgStyle.left = props.left;
            imgStyle.top = props.top;
        }

        if (this.galleryPhotos.length > 3 || window.innerWidth < 665) {
            imgStyle.width = props.photo.width;
            imgStyle.height = props.photo.height;
        } else {
            imgStyle.width = this.calculateAspectRatioHeight(props.photo.width, props.photo.height, 200);
            imgStyle.height = 200;
        }

        return (<NavLink
            className={"gallery-image-link"}
            key={"photoImage-" + props.index}
            to="#"
            onClick={() => {
                this.setState({ photoIndex: props.index, isOpen: true });
            }}
            location={{}}
        >
            <img
                style={imgStyle}
                className="img-fluid"
                src={props.photo.src}
            />
            <div className="removeButtonContainer" onClickCapture={(e) => this.onDeletePhoto(e, (props.photo as any).photoId)}>
                <i className="icon simple-icon-trash" />
            </div>
        </NavLink>)
    }

    get folderInfo(): { Name: string } {
        let data = {
            Name: ""
        }

        if (this.state.data) {
            data.Name = this.state.data.CurrentFolderName
        }

        return data;
    }

    get photos(): IPhoto[] {
        if (this.state.data) {
            return this.state.data.eventFolderPhotos.PhotoList;
        }
        return [];
    }

    get galleryPhotos(): { src: string, width: number, height: number }[] {
        return this.photos.filter(x => this.state.sizes[x.Id] != null).map<{ src: string, width: number, height: number }>(x => { return { photoId: x.Id, src: x.UrlThumbnail, width: this.state.sizes[x.Id].width, height: this.state.sizes[x.Id].height } });
    }

    onDeletePhoto = (e, photoId: number) => {
        e.stopPropagation();
        NotificationManager.info("", 'Deleting photo...', 3000, null, null, '');
        this.eventClient.DeletePhoto(photoId).then((response) => {
            if (response.data.Data) {
                var photos = this.state.data.eventFolderPhotos.PhotoList as IPhoto[];
                let photoIndex = photos.findIndex(x => x.Id == photoId);
                if (photoId > -1) {
                    let newPhotos = [...photos];
                    newPhotos.splice(photoIndex, 1);
                    this.setState({
                        ...this.state,
                        data: {
                            ...this.state.data,
                            eventFolderPhotos: {
                                ...this.state.data.eventFolderPhotos,
                                PhotoList: newPhotos
                            }
                        }
                    });
                    NotificationManager.success("", 'Photo deleted', 3000, null, null, '');
                }
            }

        });
    }

    get paginationOptions() {
        if (this.state.data == null) {
            return {
                page: this.props.match.params.page ? this.props.match.params.page : 1,
                totalItems: 0,
                pages: 0
            }
        }
        return {
            page: this.props.match.params.page ? this.props.match.params.page : 1,
            totalItems: this.state.data.photosCount,
            pages: Math.ceil(this.state.data.photosCount / 50)
        }
    }

    get parallaxData() {
        if (this.state.data == null) {
            return null;
        }

        return {
            CoverPhoto: this.state.data.eventsList.CoverPhotoUrl,
            Top: this.state.data.eventFolderPhotos.CoverOffset ? this.state.data.eventFolderPhotos.CoverOffset : 0,
            ObjectId: Number(this.state.data.eventFolderPhotos.Id),
            Mode: "folder",
            CanEdit: true
        }
    }

    onChangePage = (page: number) => {
        if (page == 1) {
            this.setState({ redirect: `/photo-hosting/event/${this.props.match.params.eventId}/${this.props.match.params.folderId}` });
        } else {
            this.setState({ redirect: `/photo-hosting/event/${this.props.match.params.eventId}/${this.props.match.params.folderId}/${page}` });
        }

    }

    render() {
        if (!this.props.user) {
            return (<Redirect to={`/redirect?url=${`${BASE_URL}/Event/EventFolderPhotos?folderId=${(this.props as any).match.params.folderId}`}`} />)
        }

        if (this.state.redirect) {
            return (<Redirect to={this.state.redirect} />)
        }

        return (< >
            {this.state.loading && <div className="loading" />}
            {!this.state.loading && <><Row>
                <Colxx xxs="4">
                    <div className="d-flex">
                        <div className="d-inline-block mr-4">
                            <Link to={`/photo-hosting/event/${this.props.match.params.eventId}`}>
                                <Button
                                    style={{ fontSize: "0.8rem" }}
                                    color="primary"
                                    size="sm"
                                    className="top-right-button"

                                >
                                    <i className="iconsminds-back" /> BACK
                                </Button>
                            </Link>

                        </div>
                        <div className="d-inline-block mr-4"></div>
                        <h1>{this.folderInfo.Name}</h1>
                    </div>
                </Colxx>
                <Colxx xxs="4" className="d-flex justify-content-center align-items-center">
                    <Link to={this.state.previouseSubfolder != 0 ? `/photo-hosting/event/${this.props.match.params.eventId}/${this.state.previouseSubfolder}` : "#"}>
                        <Button
                            style={{ fontSize: "0.8rem", width: 150 }}
                            color="primary"
                            size="md"
                            className="top-right-button mr-2"
                            disabled={this.state.previouseSubfolder == 0}
                        >
                            <i className="iconsminds-arrow-left" /> PREVIOUSE
                        </Button>
                    </Link>
                    <Link to={this.state.nextSubfolder != 0 ? `/photo-hosting/event/${this.props.match.params.eventId}/${this.state.nextSubfolder}` : "#"}>
                        <Button
                            style={{ fontSize: "0.8rem", width: 150 }}
                            color="primary"
                            size="md"
                            className="top-right-button"
                            disabled={this.state.nextSubfolder == 0}
                        >
                            NEXT
                            <i className="iconsminds-arrow-right" />
                        </Button>
                    </Link>
                </Colxx>
                <Colxx xxs="4">
                    <div className="mb-2">
                        <div className="text-zero top-right-button-container">
                            <Button
                                color="primary"
                                size="lg"
                                className="top-right-button"
                                onClick={() => { this.setState({ showAddModal: true }) }}
                            >
                                <i className="simple-icon-plus" /> ADD NEW PHOTOS
                            </Button>
                            {'  '}

                        </div>


                    </div>

                </Colxx>

                <Colxx xxs="12">
                    <Separator className="mb-5" />
                </Colxx>
            </Row>
                <Row className="mb-1">
                    <Colxx xxs="12">
                        {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                    </Colxx>
                </Row>
                <div className="disable-text-selection">
                    <Row>
                        <Colxx xxs="12 social-image-row gallery photosView">
                            <Gallery targetRowHeight={200} margin={5} photos={this.galleryPhotos} renderImage={this.imageRenderer} />
                        </Colxx>
                    </Row>
                    <Row>
                        <Pagination
                            currentPage={Number(this.paginationOptions.page)}
                            totalPage={this.paginationOptions.pages}
                            onChangePage={(i) => { this.onChangePage(i) }}
                        />
                    </Row>
                </div>
                {this.state.isOpen && (
                    <Lightbox
                        mainSrc={this.photos[this.state.photoIndex].Url}
                        nextSrc={this.photos[(this.state.photoIndex + 1) % this.photos.length].Url}
                        prevSrc={this.photos[(this.state.photoIndex + this.photos.length - 1) % this.photos.length].Url}
                        onCloseRequest={() => this.setState({ isOpen: false })}
                        onMovePrevRequest={() =>
                            this.setState({ photoIndex: (this.state.photoIndex + this.photos.length - 1) % this.photos.length })
                        }
                        onMoveNextRequest={() =>
                            this.setState({ photoIndex: (this.state.photoIndex + 1) % this.photos.length })
                        }
                    />
                )}
                {this.photos.map((image, index) => {
                    return (<ImageSizer key={"imageSizer-" + index} src={image.UrlThumbnail} onLoad={(naturalWidth, naturalHeight) => { this.onLoadImage(image.Id, naturalWidth, naturalHeight) }} />)
                })}
                <GalleryUplodader onUploadCompleted={this.onUploadCompleted} folderId={this.state.folderId} modalOpen={this.state.showAddModal} toggleModal={() => this.setState({ showAddModal: !this.state.showAddModal })} />
            </>}
        </>)
    }
}

const mapStateToProps = ({ authUser }) => {
    return authUser;
};

export default connect(mapStateToProps)(PhotosView);
