import React from "react";
import classnames from "classnames";
import { IsMobile as isMobile } from '../Helpers/IsMobileHelper';


import "./ImageLightBoxContainer.scss";

export class ImageLightBoxContainer extends React.Component<any, any> {

    render() {
        const { children, innerProps } = this.props;
        return (<div className={classnames("imageLightBoxContainer", { "footerOn": this.props.hasFooter, "mobileView": isMobile() })}>{children}</div>)
    }
}