import React from "react";

export interface IImageSizerProps {
    src: string,
    onLoad: (naturalWidth: number, naturalHeight: number) => void
}


export class ImageSizer extends React.Component<IImageSizerProps> {
    constructor(props: IImageSizerProps) {
        super(props);
        this.state = { dimensions: {} };
        this.onImgLoad = this.onImgLoad.bind(this);
    }
    onImgLoad({ target: img }) {
        this.props.onLoad(img.naturalWidth, img.naturalHeight);
    }
    render() {
        return (<img style={{ width: 0, height: 0, display: "none" }} onLoad={this.onImgLoad} src={this.props.src} />);
    }
}