import * as React from "react";
import axios from "axios";
import ImageHelper, { ImageType } from "./ImageHelper";
import { BASE_URL, CONFIG } from "../../api/settings";
import { ApiResponse } from "../../models";
import { IApiResponse } from "../../api/IApiResponse";


interface Step4Props {
    files: FileList,
    watermarkHorizontal: Blob,
    watermarkVertical: Blob
    autostart: boolean,
    folderId: number,
    onUploadCompleted: () => void
}

interface Step4Stat {
    uploadedFileIndex: number,
    subscriptionEnded: boolean
}

export class Step4 extends React.Component<Step4Props, Step4Stat> {

    constructor(props: Step4Props) {
        super(props);
        this.state = {
            uploadedFileIndex: -1,
            subscriptionEnded: false
        };
    }

    componentDidMount() {
        if (this.props.autostart && this.props.files.length > 0) {
            this.upload();
        }
    }



    uploadFile = () => {
        if (this.state.uploadedFileIndex == this.props.files.length) {
            this.completeUpload();
            return;
        }

        ImageHelper.rotateImage(this.props.files[this.state.uploadedFileIndex], (rotatedImage) => {
            ImageHelper.resizeImage(rotatedImage, (file, imageType) => {
                let watermark = imageType == ImageType.Horizontal ? this.props.watermarkHorizontal : this.props.watermarkVertical;
                ImageHelper.addWatermark(file, watermark, (f) => {
                    ImageHelper.resizeImage2(f, (f2, imageType) => {
                        let formData = new FormData();
                        formData.append("file", f2, this.props.files[this.state.uploadedFileIndex].name);
                        formData.append("folderId", this.props.folderId.toString());
                        formData.append("originalFileSize", this.props.files[this.state.uploadedFileIndex].size.toString());

                        let s = { ...CONFIG }
                        s.headers["Content-Type"] = "'multipart/form-data";

                        axios.post<IApiResponse<boolean>>(BASE_URL + "/event/UploadFolderPhoto", formData, s).then((response) => {
                            if (response.data.Error == "SubscriptionEventEnded") {
                                this.setState({
                                    subscriptionEnded: true
                                });
                            } else {
                                this.setState((prevState) => ({
                                    uploadedFileIndex: prevState.uploadedFileIndex + 1,
                                }), () => {
                                    this.uploadFile();
                                });
                            }


                        }).catch(() => {
                            this.uploadFile();
                        });
                    });
                });

            });
        })
    }

    completeUpload = () => {
        this.props.onUploadCompleted();
    }

    upload = () => {
        this.setState((prevState) => ({
            uploadedFileIndex: 0
        }), () => {
            this.uploadFile();
        });
    }

    get uploading() {
        return this.state.uploadedFileIndex < this.props.files.length && this.state.uploadedFileIndex > -1;
    }

    get uploadCompleted() {
        return this.state.uploadedFileIndex == this.props.files.length;
    }

    render() {
        return (<div className="row" style={{ width: "100%", textAlign: "center" }}>
            {this.state.subscriptionEnded && <div>Subscription ended.</div>}
            {this.uploading && !this.state.subscriptionEnded && <div> Uploading... {this.state.uploadedFileIndex + 1}/{this.props.files.length}</div>}
            {this.uploadCompleted && <div> Upload completed!</div>}
        </div>);
    }
}

export default Step4;