import React, { Suspense } from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import AppLayout from '../../layout/AppLayout';

const DefaultView = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './default')
);

const Products = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './products')
);

const Settings = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './settings')
);

const Overview = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './overview')
);

const Updates = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './updates')
);

const Categories = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './categories')
);

const Attributes = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './attributes')
);

const ProductWizard = React.lazy(() =>
  import(/* webpackChunkName: "dashboard-default" */ './wizard/WizardPage')
);

// const SliderSettings = React.lazy(() => {
//   import('./updates/sliderSettings')
// });


const PhotoOrder = ({ match }) => (
  <AppLayout>
    <div className="photoorder-wrapper">
      <Suspense fallback={<div className="loading" />}>
        <Switch>
          <Route path={`${match.url}/overview`} render={(props) => <Overview {...props} />} />
          <Route exact path={`${match.url}/products/wizard`} render={(props) => <ProductWizard {...props} />} />
          <Route exact path={`${match.url}/products`} render={(props) => <Products {...props} />} />
          <Route exact path={`${match.url}/categories`} render={(props) => <Categories dataKey="ProductCategories" {...props} />} />
          <Route exact path={`${match.url}/attributes`} render={(props) => <Attributes dataKey="ProductAttributes" {...props} />} />
          <Route exact path={`${match.url}/products2/categories`} render={(props) => <Categories dataKey="ProductCategories2" {...props} />} />
          <Route exact path={`${match.url}/products2/attributes`} render={(props) => <Attributes dataKey="ProductAttributes2" {...props} />} />
          <Route path={`${match.url}/settings`} render={(props) => <Settings {...props} />} />
          <Route path={`${match.url}/updates`} render={(props) => <Updates {...props} />} />
          <Route path={`${match.url}`} render={(props) => <DefaultView {...props} />} />
          <Redirect to="/error" />
        </Switch>
      </Suspense>
    </div>
  </AppLayout>
);
export default PhotoOrder;
