import React from "react";
import { Redirect } from 'react-router-dom';
import { Row, Card, NavLink, CardTitle, FormGroup, Label, Button, Input } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import IntlMessages from "../../../helpers/IntlMessages";
import { Formik, Field, Form } from "formik";
import { EventClient } from "../../../api/eventsClient";
import { NotificationManager } from "../../../components/common/react-notifications";

export class EventLoginView extends React.Component<any, any> {

    eventCient: EventClient;

    state = {
        loading: false,
        redirectEvent: -1
    }


    constructor(props: any) {
        super(props);

        this.eventCient = new EventClient();
    }

    initialValues = {
        eventId: "",
        eventPassword: ""
    }

    onSubmit = (values, formik) => {
        this.setState({ loading: true })
        this.eventCient.CheckEventPassword({ EventId: values.eventId, EvnPassword: values.eventPassword }).then(response => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, "Error", 3000, null, null, '');
            } else {
                this.setState({ redirectEvent: values.eventId })
            }
            formik.setSubmitting(false);
        }).finally(() => {
            formik.setSubmitting(false);
            this.setState({ loading: false })
        });


    }


    render() {

        if (this.state.redirectEvent > -1) return (<Redirect to={`/portal/event/${this.state.redirectEvent}`} />);

        return (<><Row style={{ height: "100vh" }}>
            <Colxx xxs="12" md="4" className="mx-auto my-auto">
                <Card className="auth-card">

                    <div className="form-side w-100">
                        <CardTitle className="mb-4">
                            Event Access
                        </CardTitle>
                        <Formik initialValues={this.initialValues} onSubmit={this.onSubmit}>
                            {({ errors, touched }) => (
                                <Form className="av-tooltip tooltip-label-bottom">
                                    <FormGroup row>
                                        <Colxx sm={6}>
                                            <FormGroup>
                                                <Label for="eventId">
                                                    Event ID
                                                </Label>
                                                <Field className="form-control" name="eventId" type="text" id="eventId" placeholder={"Event ID"} />
                                            </FormGroup>
                                        </Colxx>

                                        <Colxx sm={6}>
                                            <FormGroup>
                                                <Label for="eventPassword">
                                                    Event Password
                                                </Label>
                                                <Field className="form-control" name="eventPassword" type="password" id="eventPassword" placeholder={"Event Password"} />
                                            </FormGroup>
                                        </Colxx>
                                    </FormGroup>
                                    <div className="d-flex justify-content-between align-items-center">
                                        <Button color="primary" type="submit" className={`btn-shadow btn-multiple-state ${this.state.loading ? 'show-spinner' : ''}`} size="lg">
                                            <span className="spinner d-inline-block">
                                                <span className="bounce1" />
                                                <span className="bounce2" />
                                                <span className="bounce3" />
                                            </span>
                                            <span className="label">
                                                SEARCH
                                            </span>
                                        </Button>
                                    </div>
                                </Form>
                            )}
                        </Formik>
                    </div>
                </Card>
            </Colxx>
        </Row>
        </>);
    }
}

export default EventLoginView;