import React from "react";
import { Row, Button } from "reactstrap";
import { IEventListItemViewModel } from "../../../models/EventListItemViewModel";
import Pagination from "../../../containers/pages/Pagination";
import { Colxx, Separator } from "../../../components/common/CustomBootstrap";
import Breadcrumb from '../../../containers/navs/Breadcrumb';
import { EventClient, IEvent, IEventClientListResponse } from "../../../api/eventsClient";
import { connect } from 'react-redux';
import { NotificationManager } from "../../../components/common/react-notifications";
import ParallaxItem from "../../../components/parallax/parallaxItem";
import { EventListItemView } from "../../photoHosting/events/event";
import EventItemView from "./EventItemView";
import { ClientLayoutHeader } from "../../../layout/ClientLayoutHeader";

export interface Event {
    id: number,
    img: string,
    status: string,
    statusColor: string,
    date: string
}

export interface IEventDefaultViewState {
    events: IEvent[],
    currentPage: number,
    totalPage: number
    response: IEventClientListResponse | null
}

export class PhotographerView extends React.Component<any, IEventDefaultViewState> {
    pageSizes = [4, 8, 12, 20];

    eventClient: EventClient;

    constructor(props) {
        super(props);

        this.state = {
            currentPage: 1,
            totalPage: 1,
            events: [],
            response: null
        }
        this.eventClient = new EventClient();


    }

    get events() {
        let ev = [...this.state.events];
        ev.reverse();
        return ev;
    }

    componentDidMount() {
        console.log(this.props);
        this.populateData();
    }

    populateData = () => {
        this.eventClient.List(this.props.match.params.photographerId).then((response) => {
            if (response.data.Error) {
                NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
            } else {
                if (response.data.Data != null) {
                    this.setState({
                        currentPage: response.data.Data.Page,
                        totalPage: response.data.Data.PageCount,
                        events: response.data.Data.EventsList,
                        response: response.data.Data
                    });
                }
            }

        });
    }



    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: this.state.response.CoverPhotoUrl,
            Top: this.state.response.CoverOffset ? this.state.response.CoverOffset : 0,
            ObjectId: Number(this.state.response.PhotographerId),
            Mode: "photographer",
            CanEdit: false
        }
    }

    render() {
        return (<>
            <ClientLayoutHeader searchText="Event ID" searchUrl="/portal" />
            <Row style={{ marginBottom: "18px" }}>
                <Colxx className={"p-0"} xxs="12">
                    {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                </Colxx>
            </Row>
            <div className="disable-text-selection">
                <Row>
                    {this.events?.map((event, index) => {
                        return (<EventItemView key={event.Id} event={event} />)
                    })}
                    <Pagination
                        currentPage={this.state.currentPage}
                        totalPage={this.state.totalPage}
                        onChangePage={(i) => { }}
                    />
                </Row>
            </div>

        </>)
    }
}

export default PhotographerView;
