import Axios from "axios";
import * as settings from "./settings";
import { IApiResponse } from "./IApiResponse";
import { IEventPasswordViewModel } from "../models/EventPasswordViewModel";
import { OrderFavourites } from "../views/portal/folder/EventFolderPhotos/Models/Favourites";
import store2 from "store2";

export interface IEvent {
    Id: number,
    FileSrc: string,
    FileName: string,
    FileGuid: string,
    Name: string,
    IsEditMode: boolean,
    PassProtected: boolean,
    IsHidden: boolean,
    NoPassKey: string
}

export interface IEventClientListResponse {
    EventsList: IEvent[]
    Page: number,
    PageSize: number,
    PageCount: number,
    EditMode: boolean,
    SetPrivateMode: boolean,
    CustomBackgroundUrl: string,
    FontColor: string,
    CoverPhotoUrl: string,
    CoverOffset: number,
    PhotographerId: string,
    IsNewAccountUrl: boolean,
    TypeOfMenu: number,
    Notification: string
}

export interface IAddEditEventDialogViewModel {
    Id: number,
    Name: string,
    AdditionalInfo: string,
    IsHidden: boolean,
    FirstFormat: string,
    FirstFormatPrice: number,
    SecondFormat: string,
    SecondFormatPrice: number,
    ThirdFormat: string,
    ThirdFormatPrice: number,
    FourthFormat: string,
    FourthFormatPrice: number,
    NoPricing: boolean,
    TimeToDelete: number,
    EmailToClient: string,
    Password: string,
    LinkForEmail: string,
    NoPassKey: string,
    File: File | null
}

export interface IAddEditFolderDialogViewModel {
    Id: number,
    EventId: number,
    Name: string,
    Password: string,
    FolderCoverPhoto: File
}

export enum EmailLinkType { LinkToGalery = 1, DirectLinkToEvent = 2, AddTourPersonal = 3 }

export enum PictureFormatOrderEnum {
    Album = 0,
    Standard = 1,
    Large = 2,
    Special = 3
}

export class EventClient {

    List(id: number, page?: number) {
        return Axios.get<IApiResponse<IEventClientListResponse>>(`${settings.BASE_URL}/event/list/${id}`, settings.CONFIG);
    }

    Delete(id: number) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/event/DeleteEvent/${id}`, { id: id }, settings.CONFIG);
    }

    AddEvent(event: IAddEditEventDialogViewModel) {
        event.Id = 0;
        return this.SaveEvent(event);
    }

    EditEvent(event: IAddEditEventDialogViewModel) {
        delete (event as any).FaceBookModel;
        return this.SaveEvent(event);
    }

    SaveEvent(event: IAddEditEventDialogViewModel) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";

        let formData = new FormData();

        for (let key in event) {
            if (event[key]) {
                formData.append(key, event[key]);
            }

        }

        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/event/SaveEvent`, formData, s);
    }

    EventFoldersList(eventId: number, page?: number) {
        if (page != null) {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFoldersList?eventId=${eventId}&page=${page}`, settings.CONFIG);
        } else {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFoldersList?eventId=${eventId}`, settings.CONFIG);
        }
    }

    GetAddEventDialog(eventId: number) {
        return Axios.post<IApiResponse<IAddEditEventDialogViewModel>>(`${settings.BASE_URL}/event/GetAddEventDialog/${eventId}`, {}, settings.CONFIG);
    }

    SaveFolder(folder: IAddEditFolderDialogViewModel) {
        let s = { ...settings.CONFIG }
        s.headers["Content-Type"] = "'multipart/form-data";

        let formData = new FormData();

        for (let key in folder) {
            formData.append(key, folder[key]);
        }

        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/event/SaveFolder`, formData, s);
    }

    DeleteFolder(eventId: number, folderId: number) {
        return Axios.get<IApiResponse<boolean>>(`${settings.BASE_URL}/event/DeleteFolder?eventId=${eventId}&folderId=${folderId}`, settings.CONFIG);
    }

    EventFolderPhotos(folderId: number, page?: number) {
        if (page) {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFolderPhotos?folderId=${folderId}&page=${page}`, settings.CONFIG);
        } else {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFolderPhotos?folderId=${folderId}`, settings.CONFIG);
        }


    }

    DeletePhoto(photoId: number) {
        return Axios.get<IApiResponse<boolean>>(`${settings.BASE_URL}/Event/DeletePhoto?photoId=${photoId}`, settings.CONFIG);

    }

    GenerateEventXML(eventId: number) {
        return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/GenerateEventXML?eventId=${eventId}`, settings.CONFIG);
    }

    SetCoverOffset(objectId: number, offset: number, mode: string) {
        return Axios.post(`${settings.BASE_URL}/event/SetCoverOffset`, { objectId: objectId, offset: offset, mode: mode }, settings.CONFIG);
    }

    CheckEventPassword(data: IEventPasswordViewModel) {
        return Axios.post<IApiResponse<boolean>>(`${settings.BASE_URL}/event/CheckEventPassword`, data, settings.CONFIG);
    }

    ChangeFolder(folderId: number, backOrNext: boolean) {
        return Axios.get<IApiResponse<boolean>>(`${settings.BASE_URL}/event/ChangeFolder?folderId=${folderId}&backOrNext=${backOrNext}`, settings.CONFIG);
    }

    EventFolderPhotosFavourites(folderId: number) {
        return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFolderPhotosFavourites?folderId=${folderId}`, settings.CONFIG);

    }

    EventFolderPhotosFavourites2(folderId: number) {
        var orderFav = store2.namespace("Favourites").getAll();
        return Axios.post<IApiResponse<any>>(`${settings.BASE_URL}/event/EventFolderPhotosFavourites2`, { folderId: folderId, cookies: orderFav }, settings.CONFIG);

    }

    EventOrderSummary(folderId: number, orderBy?: PictureFormatOrderEnum) {
        if (orderBy) {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventOrderSummary?folderId=${folderId}&orderBy=${orderBy}`, settings.CONFIG);
        } else {
            return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventOrderSummary?folderId=${folderId}`, settings.CONFIG);
        }
    }

    EventOrderSummary2(folderId: number, orderBy?: PictureFormatOrderEnum) {
        var orderFav = store2.namespace("EventPhotoCart").getAll();
        return Axios.post<IApiResponse<any>>(`${settings.BASE_URL}/event/EventOrderSummary2`, { folderId: folderId, orderBy: orderBy, cookies: orderFav }, settings.CONFIG);
    }

    EventNoPassword(guid: string) {
        return Axios.get<IApiResponse<any>>(`${settings.BASE_URL}/event/EventNoPassword?guid=${guid}`, settings.CONFIG);
    }

    GetXmlForOrder(data: any) {
        console.log(data);
        return Axios.post<IApiResponse<any>>(`${settings.BASE_URL}/event/GetXmlForOrder`, data, settings.CONFIG);
    }

}