import React from "react";
import { EventClient } from "../../../api/eventsClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { Row, Button } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import { ParallaxItem } from "../../../components/parallax/parallaxItem";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'
import { Redirect, Link } from "react-router-dom";
import { ClientLayoutHeader } from "../../../layout/ClientLayoutHeader";
import EventFolderPhotos from "../folder/EventFolderPhotos/EventFolderPhotos";
import SummaryButtonFilter from "../folder/EventFolderPhotos/SummaryButton";

import "./Checkout.scss";
import { CheckoutForm } from "./CheckoutForm";
import { FormikHelpers } from "formik";

export class CheckoutView extends React.Component<any, any> {

    ref = React.createRef<EventFolderPhotos>();

    state = {
        redirect: "",
        response: null,
        page: 0,
        showSendForm: false
    }

    eventClient: EventClient;

    constructor(props: any) {
        super(props);

        this.eventClient = new EventClient();
    }

    componentDidMount() {
        this.populateData();
    }

    populateData() {
        this.setState({ response: null });
        this.eventClient.EventOrderSummary2(this.props.match.params.folderId, this.props.match.params.order ? this.props.match.params.order : null).then(response => {
            if (response.data.Redirect) {
                this.setState({ redirect: response.data.Redirect });
            } else
                if (response.data.Error) {
                    NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
                } else {
                    if (response.data.Data != null) {
                        this.setState({
                            currentPage: response.data.Data.Page,
                            totalPage: response.data.Data.PageCount,
                            eventData: response.data.Data,
                            response: response.data.Data
                        });
                    }
                }
        });
    }

    public componentDidUpdate(prevProps) {
        if (this.props.location.pathname != prevProps.location.pathname) {
            this.setState({ redirect: "" });
            this.populateData();
        }
    }



    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: (this.state.response as any).EventCoverPhotoUrl,
            Top: (this.state.response as any).CoverOffset ? (this.state.response as any).CoverOffset : 0,
            ObjectId: Number((this.state.response as any).EventId),
            Mode: "folder",
            CanEdit: false
        }
    }

    get folderTitle() {
        if (this.state.response && (this.state.response as any).eventFolderList) {
            return (this.state.response as any).CurrentFolderName;
        }
        return "";
    }

    changeFolder = (next: boolean) => {

        this.eventClient.ChangeFolder(this.props.match.params.folderId, next).then((response) => {
            this.setState({ redirect: response.data.Redirect });
        });
    }

    showOrderSendForm = () => {
        this.setState({ showSendForm: true })
    }

    toggleForm = (show: boolean) => {
        this.setState({ showSendForm: show });
    }

    onSubmit = (data: any, actions: FormikHelpers<any>) => {
        if (this.ref.current) {
            this.ref.current.sendOrder2(data).then(response => {
                if (response.data.Error) {
                    NotificationManager.error("Saving error", response.data.Error, 3000, null, null, '');
                    actions.setSubmitting(false);
                } else {
                    NotificationManager.success("Order created", "", 10000, null, null, '');
                    actions.setSubmitting(false);
                    this.setState({ showSendForm: false, redirect: `/portal/event/${this.props.match.params.eventId}` });
                }
            }).catch(() => {
                NotificationManager.error("Saving error", "Something went wrong", 3000, null, null, '');
                actions.setSubmitting(false);
            });



        } else {
            NotificationManager.error("Saving error", "Something went wrong", 3000, null, null, '');
            actions.setSubmitting(false);
        }
    }

    onResetCart = () => {
        this.setState({ redirect: `/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}` })
    }
    onChangeFormat = (format) => {
        if (this.props.match.params.order != format) {
            if (format != null) {
                this.setState({ redirect: `/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}/checkout/format/${format}` });
            } else {
                this.setState({ redirect: `/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}/checkout` });
            }
        }
    }

    render() {

        if (this.state.redirect) {
            return (<Redirect to={this.state.redirect} />);
        }

        return (<>
            <div className="client-container-header-checkout">
                <Row className="h-100">
                    <Colxx xxs="6" className={"pl-4 d-flex justify-content-flex-start align-items-center"}>
                        <Link to={`/portal/event/${this.props.match.params.eventId}/folder/${this.props.match.params.folderId}`}>
                            <Button color="light">
                                Back
                            </Button>
                        </Link>
                    </Colxx>

                    <Colxx xxs="6" className={"pr-4 text-right d-flex justify-content-end align-items-center"}>
                        <Button color="light" onClick={() => this.showOrderSendForm()}>
                            <div className="glyph-icon iconsminds-mail-send d-inline-block"></div> Send Order
                        </Button>
                    </Colxx>

                </Row>
            </div >
            <Row style={{ marginBottom: "18px" }}>
                <Colxx className={"p-0"} xxs="12">
                    {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                </Colxx>
            </Row>
            <Row style={{ marginBottom: 8 }}>
                <Colxx xxs="12">
                    {this.state.response != null && <SummaryButtonFilter
                        onChangeFormat={this.onChangeFormat}
                        onResetCart={this.onResetCart}
                        folder={(this.state.response as any).CurrentFolderName}
                        formats={(this.state.response as any).FormatInfo}
                        folderId={(this.state.response as any).Id}
                        eventId={(this.state.response as any).EventId}
                        formatFilter={(this.state.response as any).OrderBy}
                        foldersIds={(this.state.response as any).PhotoList.map(x => x.FolderId).filter((value, index, self) => { return self.indexOf(value) === index; })}
                    />}
                </Colxx>
            </Row>
            <Row>
                <Colxx xxs="12" className="text-center">
                    {this.folderTitle && <h1><span className="mr-4" style={{ cursor: "pointer" }} onClick={() => this.changeFolder(false)}><FontAwesomeIcon icon={faAngleLeft} /></span>{this.folderTitle}<span onClick={() => this.changeFolder(true)} className="ml-4" style={{ cursor: "pointer" }}><FontAwesomeIcon icon={faAngleRight} /></span></h1>}
                </Colxx>
            </Row>
            {
                this.state.response != null && <EventFolderPhotos
                    ref={this.ref}
                    canLoad={false}
                    page={this.state.page}
                    eventId={(this.state.response as any).EventId}
                    photoCount={(this.state.response as any).photosCount}
                    folderId={(this.state.response as any).Id}
                    formats={(this.state.response as any).FormatInfo}
                    isAdmin={false}
                    folder={(this.state.response as any).CurrentFolderName}
                    data={(this.state.response as any).PhotoList}
                    formatFilter={(this.state.response as any).OrderBy}
                    favouritesOnly={false}
                    foldersIds={(this.state.response as any).PhotoList.map(x => x.FolderId).filter((value, index, self) => { return self.indexOf(value) === index; })}
                />
            }
            <CheckoutForm isOpen={this.state.showSendForm} toggle={(state) => this.toggleForm(state)} onSubmit={this.onSubmit} />
        </>
        );
    }
}

export default CheckoutView;