import React from "react";
import { EventClient } from "../../../api/eventsClient";
import { NotificationManager } from "../../../components/common/react-notifications";
import { IFolder } from "../../../models/Folder";
import { Row, Pagination } from "reactstrap";
import { Colxx } from "../../../components/common/CustomBootstrap";
import ParallaxItem from "../../../components/parallax/parallaxItem";
import { SubfolderListItemView } from "../../photoHosting/events/subfolderitem";
import FolderItem from "./FolderItem";
import { Redirect } from "react-router-dom"
import { ClientLayoutHeader } from "../../../layout/ClientLayoutHeader";

export class EventView extends React.Component<any, any> {
    eventClient: EventClient;

    constructor(props: any) {
        super(props);
        this.state = {
            response: null,
            currentPage: 0,
            totalPage: 0,
            redirect: ""
        }

        this.eventClient = new EventClient();
    }

    get folders(): IFolder[] {
        if (this.state.response && this.state.response.eventFolderList && this.state.response.eventFolderList.Folders) {
            return this.state.response.eventFolderList.Folders
        }
        return [];
    }

    get eventTitle() {
        if (this.state.response && this.state.response.eventFolderList) {
            return this.state.response.eventFolderList.EventName;
        }
        return "";
    }

    componentDidMount() {
        this.eventClient.EventFoldersList(this.props.match.params.eventId).then(response => {
            if (response.data.Redirect) {
                this.setState({ redirect: response.data.Redirect });
            } else
                if (response.data.Error) {
                    NotificationManager.error(response.data.Error, 'Error', 3000, null, null, '');
                } else {
                    if (response.data.Data != null) {
                        this.setState({
                            currentPage: response.data.Data.Page,
                            totalPage: response.data.Data.PageCount,
                            eventData: response.data.Data,
                            response: response.data.Data
                        });
                    }
                }
        });
    }

    get parallaxData() {
        if (this.state.response == null) {
            return null;
        }

        return {
            CoverPhoto: this.state.response.eventFolderList.CoverPhoto,
            Top: this.state.response.eventFolderList.CoverOffset ? this.state.response.eventFolderList.CoverOffset : 0,
            ObjectId: Number(this.state.response.eventFolderList.EventId),
            Mode: "event",
            CanEdit: false
        }
    }

    firstFolderId = () => {
        if (this.state.response != null && this.state.response.eventFolderList != null && this.state.response.eventFolderList.Folders != null) {
            return this.state.response.eventFolderList.Folders[0].FolderId;
        }

        return "";
    }

    render() {
        if (this.state.redirect) {
            return (<Redirect to={this.state.redirect} />);
        }

        return (<>
            <ClientLayoutHeader checkoutUrl={`/portal/event/${this.props.match.params.eventId}/folder/${this.firstFolderId()}/checkout`} favouriteUrl={`/portal/event/${this.props.match.params.eventId}/folder/${this.firstFolderId()}/favourites`} />
            <Row style={{ marginBottom: "18px" }}>
                <Colxx className={"p-0"} xxs="12">
                    {this.parallaxData != null && <ParallaxItem canEdit={this.parallaxData.CanEdit} coverPhoto={this.parallaxData.CoverPhoto} top={this.parallaxData.Top} mode={this.parallaxData.Mode} objectId={this.parallaxData.ObjectId} />}
                </Colxx>
            </Row>
            <Row>
                <Colxx xxs="12" className="text-center">
                    <h1>{this.eventTitle}</h1>
                </Colxx>
            </Row>
            <Row>

                {this.folders.map((subfolder, index) => {
                    return (<FolderItem key={subfolder.FolderId} eventId={this.props.match.params.eventId} data={subfolder} />);
                })}
                {/* <Pagination
                    currentPage={this.state.currentPage}
                    totalPage={this.state.totalPage}
                    onChangePage={(i) => { }}
                /> */}
            </Row>
        </>)
    }
}

export default EventView;