import Resizer from "react-image-file-resizer";
import * as watermarkjs from "watermarkjs";
//import * as blueImp from "blueimp-load-image/js/load-image-orientation.js";


export enum ImageType { Horizontal, Vertical }

export function base64toBlob(b64Data: string, contentType: string = '', sliceSize: number = 512): Blob {
    let byteCharacters = atob(b64Data.replace(/^data:image\/(png|jpeg|jpg);base64,/, ''));
    let byteArrays: Uint8Array[] = [];

    for (let offset = 0; offset < byteCharacters.length; offset += sliceSize) {
        const slice = byteCharacters.slice(offset, offset + sliceSize);

        const byteNumbers = new Array(slice.length);
        for (let i = 0; i < slice.length; i++) {
            byteNumbers[i] = slice.charCodeAt(i);
        }

        let byteArray: Uint8Array = new Uint8Array(byteNumbers);
        byteArrays.push(byteArray);
    }

    let blob = new Blob(byteArrays, { type: contentType });
    return blob;
}

export function blobToBase64(image: Blob, callback: (base64Image: string) => void) {
    var reader = new FileReader();
    reader.onloadend = function () {
        callback((reader.result as string));
    }
    reader.readAsDataURL(image);
}

export function addWatermark(file: Blob, watermark: Blob, callback: (file: Blob) => void) {
    if (watermark == null) {
        callback(file);
    } else {
        watermarkjs([file, watermark])
            .blob(watermarkjs.image.atPos(() => { return 0 }, () => { return 0 }, 1))
            .then(callback);
    }
}

export function resizeImage2(file: Blob, callback: (file: Blob, imageType: ImageType) => void, fileType: string = "JPEG", quality: number = 70) {
    var fr = new FileReader;
    fr.onload = () => {
        var img = new Image;
        let imgType: ImageType = ImageType.Horizontal
        img.onload = () => {
            let ratio = 1;
            if (img.width >= img.height) {
                ratio = 700 / img.height;
                imgType = ImageType.Horizontal
            }
            else {
                imgType = ImageType.Vertical;
                ratio = 700 / img.width;
            }
            Resizer.imageFileResizer(file, img.width * ratio, img.height * ratio, fileType, quality, 0,
                uri => {
                    callback(base64toBlob(uri as any), imgType);
                },
                "base64"
            );
        };
        (img.src as any) = fr.result;
    };
    fr.readAsDataURL(file);


}

export function rotateImage(file: File, callback: (file: Blob) => void) {
    getOrientatio(file, (orientation) => {
        resetOrientation(file, orientation, (base64) => {
            callback(base64toBlob(base64));
        });
    });
}

export function resetOrientation(file: File, srcOrientation, callback) {
    var fr = new FileReader;
    fr.onload = () => {
        var img = new Image();

        img.onload = function () {
            var width = img.width,
                height = img.height,
                canvas = document.createElement('canvas'),
                ctx = canvas.getContext("2d") as CanvasRenderingContext2D;

            // set proper canvas dimensions before transform & export
            if (4 < srcOrientation && srcOrientation < 9) {
                canvas.width = height;
                canvas.height = width;
            } else {
                canvas.width = width;
                canvas.height = height;
            }

            // transform context before drawing image
            switch (srcOrientation) {
                case 2: ctx.transform(-1, 0, 0, 1, width, 0); break;
                case 3: ctx.transform(-1, 0, 0, -1, width, height); break;
                case 4: ctx.transform(1, 0, 0, -1, 0, height); break;
                case 5: ctx.transform(0, 1, 1, 0, 0, 0); break;
                case 6: ctx.transform(0, 1, -1, 0, height, 0); break;
                case 7: ctx.transform(0, -1, -1, 0, height, width); break;
                case 8: ctx.transform(0, -1, 1, 0, 0, width); break;
                default: break;
            }

            // draw image
            ctx.drawImage(img, 0, 0);

            // export base64
            callback(canvas.toDataURL());
        };

        (img.src as any) = fr.result;
    };
    fr.readAsDataURL(file);
};


export function resizeImage(file: Blob, callback: (file: Blob, imageType: ImageType) => void, fileType: string = "JPEG", quality: number = 70) {
    var fr = new FileReader;
    fr.onload = () => {
        var img = new Image;
        let imgType: ImageType = ImageType.Horizontal
        img.onload = () => {
            let ratio = 1;
            if (img.width >= img.height) {
                ratio = 700 / img.height;
                imgType = ImageType.Horizontal
            }
            else {
                imgType = ImageType.Vertical;
                ratio = 700 / img.width;
            }
            Resizer.imageFileResizer(file, img.width * ratio, img.height * ratio, fileType, quality, 0,
                uri => {
                    callback(base64toBlob(uri as any), imgType);
                },
                "base64"
            );
        };
        (img.src as any) = fr.result;
    };
    fr.readAsDataURL(file);


}

export default {
    base64toBlob,
    blobToBase64,
    resizeImage,
    resizeImage2,
    addWatermark,
    rotateImage
}


function getOrientatio(file: File, callback: Function) {
    var reader = new FileReader();

    reader.onload = (event: ProgressEvent) => {

        if (!event.target) {
            return;
        }

        const file = event.target as FileReader;
        const view = new DataView(file.result as ArrayBuffer);

        if (view.getUint16(0, false) != 0xFFD8) {
            return callback(-2);
        }

        const length = view.byteLength
        let offset = 2;

        while (offset < length) {
            if (view.getUint16(offset + 2, false) <= 8) return callback(-1);
            let marker = view.getUint16(offset, false);
            offset += 2;

            if (marker == 0xFFE1) {
                if (view.getUint32(offset += 2, false) != 0x45786966) {
                    return callback(-1);
                }

                let little = view.getUint16(offset += 6, false) == 0x4949;
                offset += view.getUint32(offset + 4, little);
                let tags = view.getUint16(offset, little);
                offset += 2;
                for (let i = 0; i < tags; i++) {
                    if (view.getUint16(offset + (i * 12), little) == 0x0112) {
                        return callback(view.getUint16(offset + (i * 12) + 8, little));
                    }
                }
            } else if ((marker & 0xFF00) != 0xFF00) {
                break;
            }
            else {
                offset += view.getUint16(offset, false);
            }
        }
        return callback(-1);
    };

    reader.readAsArrayBuffer(file);
}